export default class PhoneUtils {
    static loadAudioUrl(key, url, local) {
        this.loadingAudioMap[key] = true;
        this.audioErrorResourceRef[key] = 0;
        if (flashvars.client === 'ios') {
            let Arg = {key:key, url: url, local:local};
            this.audioCountLoadByPhone.total++;
            if(flashvars.offline ==='1'){
                javascript:loadAudioUrl(JSON.stringify(Arg));
            }else{
                window.webkit && window.webkit.messageHandlers.loadAudioUrl.postMessage(JSON.stringify(Arg));
            }
        }
    }

    static playMusic(key, volume, loop,precent = 1) {
        const musicKey = key ? key : this.musicKey;
        const v = (!volume && volume !== 0) ? 1 : volume;
        const l = loop === undefined || loop === null ? true : loop;
        this.musicInfo.key = musicKey;
        this.musicInfo.volume = v * precent;
        this.musicInfo.prevolume = v;
        this.musicInfo.inheritvolume = -1;
        this.musicInfo.tweenId = parseInt(Math.random()*100);
        this.playAudioUrl(musicKey, v, l, 'music',precent);
    }

    static stopMusic(isclear = true) {
        this.stopAudioUrl('music');
        isclear && (this.musicInfo.key = '');
    }

    static playInheritMusic(volume,precent=1) {
        this.musicInfo.inheritvolume = volume; //插播的音量
        this.setAudioUrlVolume('music', volume * precent);
    }

    static tweenFromMusic(volume, duration, delay) {
        return new Promise((resolve, reject) => {
            if (duration <= 10) {
                this.musicInfo.volume = volume;
                this.setAudioUrlVolume('music', volume);
                return resolve();
            }

            let nTimers = duration / 20;
            let nStep = (this.musicInfo.volume - volume) / nTimers;
            let nCur = volume;

            this.musicInfo.tweenId = parseInt(Math.random()*100);
            let tweenId = this.musicInfo.tweenId;

            const doEase = () => {
                const  that = this;
                return function () {
                    if ((that.musicInfo.volume > nCur && nStep > 0 || that.musicInfo.volume < nCur && nStep < 0)
                        && tweenId === that.musicInfo.tweenId) {
                        nCur += nStep;
                        if (nCur > 1) nCur = 1;
                        else if (nCur < 0) nCur = 0;
                        that.setAudioUrlVolume('music', nCur);
                        that.musicInfo.volume = nCur;
                        setTimeout(doEase(), 20);
                    }
                    else {
                        that.musicInfo.volume = volume;
                        that.musicInfo.prevolume = volume;
                        return resolve();
                    }
                }
            };
            setTimeout(doEase(), delay);
        })
    }

    static tweenToMusic(volume, duration, delay) {
        return new Promise((resolve, reject) => {
            if (duration <= 10) {
                this.musicInfo.volume = volume;
                this.setAudioUrlVolume('music', volume);
                return resolve();
            }

            let nTimers = duration / 20;
            let nStep = (volume - this.musicInfo.volume) / nTimers;
            let nCur = this.musicInfo.volume;

            this.musicInfo.tweenId = parseInt(Math.random()*100);
            let tweenId = this.musicInfo.tweenId;

            const doEase = () => {
                const that = this;
                return function () {
                    if ((nCur > volume && nStep < 0 || nCur < volume && nStep > 0) && tweenId === that.musicInfo.tweenId) {
                        nCur += nStep;
                        if (nCur < 0) nCur = 0;
                        else if (nCur > 1) nCur = 1;
                        that.setAudioUrlVolume('music', nCur);
                        that.musicInfo.volume = nCur;
                        setTimeout(doEase(), 20);
                    }
                    else {
                        that.musicInfo.volume = volume;
                        that.musicInfo.prevolume = volume;
                        return resolve();
                    }
                }
            };
            setTimeout(doEase(), delay);
        })
    }

    static tweenToSound(index, volume, duration, delay) {
        return new Promise((resolve, reject) => {
            if (!this.soundsInfo[index].key) {
                return resolve();
            }
            if (duration <= 10) {
                this.soundsInfo[index].volume = volume;
                this.setAudioUrlVolume('sound'+index, volume);
                return resolve();
            }

            let nTimers = duration / 20;
            let nStep = (volume - this.soundsInfo[index].volume) / nTimers;
            let nCur = this.soundsInfo[index].volume;

            this.soundsInfo[index].tweenId = parseInt(Math.random()*100);
            let tweenId = this.soundsInfo[index].tweenId;

            const doEase = () => {
                const that = this;
                return function () {
                    if ((nCur > volume && nStep < 0 || nCur < volume && nStep > 0) && tweenId === that.soundsInfo[index].tweenId) {
                        nCur += nStep;
                        if (nCur < 0) nCur = 0;
                        else if (nCur > 1) nCur = 1;
                        that.setAudioUrlVolume('sound'+index, nCur);
                        that.soundsInfo[index].volume = nCur;
                        setTimeout(doEase(), 20);
                    }
                    else {
                        that.soundsInfo[index].volume = volume;
                        that.soundsInfo[index].prevolume = volume;
                        return resolve();
                    }
                }
            };
            setTimeout(doEase(), delay);
        })
    }

    static stopSound(index) {
        if(index === -1){
            for(let i =0;i <  this.soundsInfo.length;i++){
                let s = this.soundsInfo[i];
                if(s){
                    s.key && this.stopAudioUrl('sound'+i);
                }
            }
        }else{
            const key = this.soundsInfo[index].key;
            key && this.stopAudioUrl('sound'+index);
            this.soundsInfo[index].key = '';
        }
    }

    static playSound(index, key, volume, loop,precent = 1) {
        const soundKey = key ? key : this.soundsInfo[index].key;
        const v = (!volume && volume !== 0) ? 1 : volume;
        const l = loop === undefined || loop === null ? true : loop;
        this.soundsInfo[index].key = soundKey;
        this.soundsInfo[index].volume = v * precent;
        this.soundsInfo[index].loop = l;
        this.soundsInfo[index].prevolume = v;
        this.soundsInfo[index].inheritvolume = -1;
        this.soundsInfo[index].tweenId = parseInt(Math.random()*100);
        this.playAudioUrl(soundKey, v, l, 'sound'+index,precent);
    }

    static playInheritSound(index, volume,precent=1) {
        this.soundsInfo[index].inheritvolume = volume;//插播音量
        this.setAudioUrlVolume('sound'+index, volume * precent);
    }

    static playAudioUrl(key, volume, loop, channel,precent = 1) {
        if (flashvars.client === 'ios') {
            let Arg = {key: key, volume:volume * precent, loop:loop?1:0, channel:channel};
            if(flashvars.offline == 1){
                javascript:playAudioUrl(JSON.stringify(Arg));
            }else{
                window.webkit && window.webkit.messageHandlers.playAudioUrl.postMessage(JSON.stringify(Arg));
            }
        }
    }

    static stopAudioUrl(channel) {
        if (flashvars.client === 'ios') {
            let Arg = {channel: channel};
            if(flashvars.offline == 1){
                javascript:stopAudioUrl(JSON.stringify(Arg));
            }else{
                window.webkit && window.webkit.messageHandlers.stopAudioUrl.postMessage(JSON.stringify(Arg));
            }
        }
    }
    static updateMusicVolume(channel,precent = 1){
        if(this.musicInfo){
            let volume = (this.musicInfo.inheritvolume !== undefined && this.musicInfo.inheritvolume !== -1)? this.musicInfo.inheritvolume * precent : this.musicInfo.prevolume * precent;
            this.setAudioUrlVolume(channel,volume);
        }
    }
    static updateSoundVolume(channel,precent=1){
        if(this.soundsInfo && this.soundsInfo.length > 0){
            for(let i =0;i <  this.soundsInfo.length;i++){
                let s = this.soundsInfo[i];
                if(s){
                    let volume = (s.inheritvolume !== undefined && s.inheritvolume !== -1) ? s.inheritvolume * precent : s.prevolume * precent;
                    this.setAudioUrlVolume(channel + i,volume)
                }
            }
        }
    }
    static setAudioUrlVolume(channel, volume) {

        if (flashvars.client === 'ios') {
            let Arg = {channel: channel, volume:volume};
            if(!Arg.volume && Arg.volume !== 0){ //防止穿过去的volume为null
                Arg.volume = 1;
            }
            if(channel === 'music'){
                this.musicInfo.volume = Arg.volume
            };
            if(flashvars.offline === '1'){
                javascript:setAudioUrlVolume(JSON.stringify(Arg));
            }else{
                window.webkit && window.webkit.messageHandlers.setAudioUrlVolume.postMessage(JSON.stringify(Arg));
            }
        }
    }

    static removeAudioUrl(key) {
        if (key === this.musicInfo.key || this.soundsInfo.some(k => {return key === k})) {
            return;
        }

        let Arg = {key: key};
        this.audioResourceRef[key] = 0;
        if(flashvars.offline == 1){
            javascript:removeAudioUrl(JSON.stringify(Arg));
        }else{
            window.webkit && window.webkit.messageHandlers.removeAudioUrl.postMessage(JSON.stringify(Arg));
        }
    }

    static isAudioInCache(key) {
        return !!this.audioResourceRef[key];
    }

    static isAudioError(key){
        return !!this.audioErrorResourceRef[key];
    }

    static isLoadingAudio(key) {
        return !!this.loadingAudioMap[key];
    }

    static audioCanPlayCallBack(key) {
        delete this.loadingAudioMap[key];
        this.audioCountLoadByPhone.loaded++;
        this.load.onSoundExtendLoad.dispatch(key);
        this.audioResourceRef[key] = 1;
    }

    static audioErrorCallBack(key) {
        delete this.loadingAudioMap[key];
        this.audioCountLoadByPhone.loaded++;
        this.load.onSoundExtendLoadError.dispatch(key);
        this.audioResourceRef[key] = 0;
        this.audioErrorResourceRef[key] = 1;
    }
}
PhoneUtils.loadingAudioMap = {};
PhoneUtils.audioCountLoadByPhone = {loaded:0, total:0};
PhoneUtils.load = null;
PhoneUtils.musicInfo = {key:'', volume:1, tweenId:0};
PhoneUtils.soundsInfo = [{key:'', volume:1, loop:false, tweenId:0}, {key:'', volume:1, loop:false, tweenId:0}, {key:'', volume:1, loop:false, tweenId:0}
                        , {key:'', volume:1, loop:false, tweenId:0}, {key:'', volume:1, loop:false, tweenId:0}, {key:'',volume:1, loop:false, tweenId:0}
                        , {key:'', volume:1, loop:false, tweenId:0}, {key:'', volume:1, loop:false, tweenId:0}, {key:'', volume:1, loop:false, tweenId:0}
                        , {key:'', volume:1, loop:false, tweenId:0}];
PhoneUtils.audioResourceRef = [];
PhoneUtils.audioErrorResourceRef = []; //声音加载出错