import puremvc from 'puremvc';
import RePhaser from '../../common/RePhaser.js';
import FontUtils from '../../common/FontUtils.js';

puremvc.define
(
    {
        name: 'diygamePlayer.view.mediator.PlaybackMediator',
        parent: puremvc.Mediator
    },
    {
        listNotificationInterests: function () {
            return [
                diygamePlayer.AppConstants.SCENE_GOTO_PLAYBACK,
                diygamePlayer.AppConstants.ADD_PLAYBACK_CONTENT
            ]
        },

        handleNotification: function (note) {
            switch (note.getName()) {
                case diygamePlayer.AppConstants.SCENE_GOTO_PLAYBACK:
                    this.callbackInfo = note.getBody();
                    this.showPlaybackLayer();
                    break;
                case diygamePlayer.AppConstants.ADD_PLAYBACK_CONTENT:
                    const body = note.getBody();
                    this.addPlaybackContent(body);
                    break;
                default:
                    break;
            }
        },

        onRegister: function () {
            this.sceneProxy = this.facade.retrieveProxy(diygamePlayer.model.proxy.SceneProxy.NAME);
            const sceneMediator = this.facade.retrieveMediator(diygamePlayer.view.mediator.SceneMediator.NAME);
            this.game = sceneMediator.viewComponent && sceneMediator.viewComponent.game;
            this.viewComponent = sceneMediator.viewComponent;
        },

        addTextContent: function (x, y, width, height, content, objText) {
            const fontStyle = {boundsAlignH: 'center', boundsAlignV: 'middle', wordWrap: true};
            let ff = '';
            ff += objText.fontWeight ? objText.fontWeight + ' ' : 'normal ';
            ff += objText.fontSize ? parseInt(objText.fontSize) + 'px ' : '';
            ff += objText.fontFamily ? objText.fontFamily : '黑体';

            fontStyle.font = ff;
            fontStyle.fill = objText.color;
            fontStyle.wordWrapWidth = width;
            if (this.sceneProxy.stringMap.back === content) {
                fontStyle.boundsAlignH = 'center';
                fontStyle.boundsAlignV = "middle";
            } else {
                fontStyle.boundsAlignH = 'left';
                fontStyle.boundsAlignV = 'top';
            }
            const txt = this.game.add.text(0, 0, content, fontStyle, this.sceneProxy.playBackLayer);
            txt.setTextBounds(x, y + (txt.height - parseInt(objText.fontSize || 12)) / 2, width, height);
            return txt;
        },

        addPlayback: function () {
            const {scrollBox, back, background} = this.sceneProxy.json_data.template.json.playback;

            // 背景
            const bk = this.sceneProxy.playBackLayer.create(0, 0, this.game.load.cache.checkImageKey(background.url) && background.url);
            bk.width = this.game.world.width;
            bk.height = this.game.world.height;
            bk.inputEnabled = true;
            // scrollBox
            const boxX = scrollBox.rect.x;
            const boxY = scrollBox.rect.y;
            const boxWidth = scrollBox.rect.width;
            const boxHeight = scrollBox.rect.height;
            const backgroundRect = Object.assign({x: 0, y: 0, width: boxWidth, height: boxHeight}, scrollBox.background.rect);

            const scrollBk = this.sceneProxy.playBackLayer.create(boxX + backgroundRect.x,
                boxY + backgroundRect.y,
                this.game.load.cache.checkImageKey(scrollBox.background.url) && scrollBox.background.url);
            scrollBk.width = backgroundRect.width;
            scrollBk.height = backgroundRect.height;

            const scrollPane = this.game.add.sprite(0, 0);
            const scrollX = boxX;
            const scrollY = boxY;
            scrollPane.x = scrollX;
            scrollPane.y = scrollY;
            scrollPane.name = 'scrollPane';
            this.sceneProxy.playBackLayer.add(scrollPane);

            // item
            const itemWidth = scrollBox.item.rect.width;
            const itemHeight = scrollBox.item.rect.height;
            const hSpace = Math.max(itemHeight - scrollBox.item.content.rect.height, 0);
            let dragStartPointX = 0;
            let dragStartY = null;
            let wheelEvent = false;
            const onMove = (pointer, x, y) => {
                const point = getPoint(pointer);
                const mousewheelevt = (/Firefox/i.test(navigator.userAgent)) ? "DOMMouseScroll" : "mousewheel";
                if (!point) {
                    if (wheelEvent) {
                        document.removeEventListener(mousewheelevt, onMouseWheel);
                        wheelEvent = false;
                    }
                }
                else {
                    if (!wheelEvent) {
                        document.addEventListener(mousewheelevt, onMouseWheel);
                        wheelEvent = true;
                    }
                }
            };
            const onScroll = (x, y) => {
                const height = scrollPane.children.length * itemHeight - hSpace;
                if (height < boxHeight) {
                    scrollPane.y = boxY;
                    return;
                }
                if (x !== boxX) {
                    scrollPane.x = boxX;
                }
                if (y > boxY) {
                    scrollPane.y = boxY;
                }
                else if (y + height < boxY + boxHeight) {
                    scrollPane.y = boxY + boxHeight - height;
                }
                else {
                    scrollPane.y = y;
                }
            };
            const onDragUpdate = (sprite, pointer, x, y, pt, bFromStart) => {
                let px = x;
                let py = y;
                bFromStart && (dragStartPointX = x);
                dragStartY = scrollPane.y;
                if (document.body.clientWidth < document.body.clientHeight && dragStartY !== null) {
                    py = dragStartY - x + dragStartPointX;
                }
                onScroll(px, py);
            };
            const getPoint = (pointer) => {
                let x, y;
                let w = (window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth);
                let h = (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight);
                if (w > h) {
                    if(flashvars.vertical){
                        x = pointer.worldY;
                        y = -pointer.worldX;
                    }else{
                        x = pointer.worldX;
                        y = pointer.worldY;
                    }
                }
                else {
                    if(flashvars.vertical){
                        x = pointer.worldX;
                        y = pointer.worldY;
                    }else{
                        x = pointer.worldY;
                        y = -pointer.worldX;
                    }
                }
                if (y < scrollY || y > /*boxY*/scrollY + boxHeight ||
                    x < scrollX || x > /*boxX*/scrollX + boxWidth) {
                    return null;
                }
                return {x, y}
            };
            const onInputDown = (sprite, pointer, x, y) => {
                if (!getPoint(pointer)) {
                    return sprite.input.disableDrag();
                }
                return sprite.input.enableDrag();
            };
            const onMouseWheel = e => {
                const delta = e.wheelDelta ? e.wheelDelta / 120 : -e.detail / 3;
                onScroll(0, scrollPane.y + delta * 100)
            };

            scrollPane.inputEnabled = true;
            scrollPane.input.enableDrag();
            scrollPane.input.allowHorizontalDrag = false;
            scrollPane.events.onDragUpdate.add(onDragUpdate, this);
            scrollPane.events.onInputDown.add(onInputDown, this);
            this.game.input.addMoveCallback(onMove, this);
            // mask
            const mask = this.game.add.graphics(0, 0);
            mask.beginFill(0xffffff);
            mask.drawRect(boxX, boxY, boxWidth, boxHeight);
            this.sceneProxy.playBackLayer.add(mask);
            scrollPane.mask = mask;
            // 返回按钮
            const onBtnClicked = (obj, pointer) => {
                if (pointer.pointerMode === Phaser.PointerMode.CURSOR && pointer.button !== 0) {
                    return;
                }
                this.sceneProxy.onPlaybackBackBtnClicked();
                if (this.callbackInfo) {
                    this.callbackInfo.callbackFun(this.callbackInfo.callbackArgs);
                    this.callbackInfo = null;
                }
            };
            if (!back.hidden) {
                const t = {};
                t.normal = back.normal.text;
                t.active = back.active.text;
                t.text = this.sceneProxy.stringMap.back;
                let btn_sound = null;
                if(this.sceneProxy.json_data.template.json.sound){
                    const {sound} = this.sceneProxy.json_data.template.json;
                    if(this.sceneProxy.viewComponent.game.load.cache.checkSoundKey(sound.url)){
                        btn_sound = this.sceneProxy.viewComponent.game.add.sound(sound.url, sound.volume)
                    }
                }
                const btnGroup = this.viewComponent.addButton(back.rect.x,
                    back.rect.y,
                    back.rect.width,
                    back.rect.height,
                    back.normal.background.url,
                    back.active.background.url,
                    onBtnClicked,
                    t,
                    btn_sound,
                    "playBack_button_back",
                    this.sceneProxy.playBackLayer
                );
                btnGroup.button.onInputUp.add(() => btn_sound && btn_sound.stop())
            }
            this.sceneProxy.playBackLayer.visible = false;
        },

        addPlaybackContent: function (data) {
            this.sceneProxy.playbackData.push(data);
            if (this.sceneProxy.playbackData.length > 100) {
                this.sceneProxy.playbackData.splice(0, 1);
            }
        },

        addPlaybackItem: function (data) {
            const scrollPane = this.sceneProxy.playBackLayer.getByName('scrollPane');
            const {scrollBox} = this.sceneProxy.json_data.template.json.playback;

            const keyItemBk = this.game.load.cache.checkImageKey(scrollBox.item.background.url) ? scrollBox.item.background.url : null;
            const keySubItemBk = this.game.load.cache.checkImageKey(scrollBox.item.content.background.url) ? scrollBox.item.content.background.url : null;
            const contentX = scrollBox.item.rect.x + scrollBox.item.content.rect.x;
            const contentY = scrollBox.item.rect.y + scrollBox.item.content.rect.y;

            const itemGroup = this.game.add.group();
            itemGroup.x = scrollBox.item.rect.x;
            itemGroup.y = scrollBox.item.rect.y + scrollBox.item.rect.height * scrollPane.children.length;

            const itemBackground = this.game.add.sprite(0, 0, keyItemBk);
            itemBackground.width = scrollBox.item.rect.width;
            itemBackground.height = scrollBox.item.rect.height;
            const itemContent = this.game.add.sprite(contentX, contentY, keySubItemBk);
            itemContent.width = scrollBox.item.content.rect.width;
            itemContent.height = scrollBox.item.content.rect.height;

            const itemText = this.addTextContent(contentX + 10,
                contentY,
                scrollBox.item.content.rect.width - 10,
                scrollBox.item.content.rect.height,
                "",
                scrollBox.item.content.text);
            itemText.useAdvancedWrap = true;
            itemText.updateText = RePhaser.updateText;
            itemText.advancedWordWrap = RePhaser.advancedWordWrap;
            itemText.lineSpacing = -parseInt(scrollBox.item.content.text.fontSize || 12) / 5;
            itemText.text = data;
            itemText.name = 'text';

            const mask = this.game.add.graphics(0, 0);
            mask.beginFill(0xffffff);
            mask.drawRect(contentX, contentY, itemContent.width, itemContent.height);
            itemText.mask = mask;
            itemGroup.addChild(mask);

            itemGroup.add(itemBackground);
            itemGroup.add(itemContent);
            itemGroup.add(itemText);
            scrollPane.addChild(itemGroup);
            /*if (scrollPane.children.length > 100) {
                scrollPane.removeChildAt(0);
                scrollPane.children.forEach(c => c.y -= scrollBox.item.rect.height)
            }*/
        },

        showPlaybackLayer: function () {
            /*if (this.sceneProxy.playBackLayer.children.length === 0) {
                this.addPlayback();
            }*/
            this.sceneProxy.playBackLayer.removeAll(true);
            this.addPlayback();
            this.sceneProxy.playbackData.forEach(d => {this.addPlaybackItem(d)});
            this.game.world.bringToTop(this.sceneProxy.playBackLayer);
            this.sceneProxy.playBackLayer.visible = true;
            // 滚动位置
            const {rect, item} = this.sceneProxy.json_data.template.json.playback.scrollBox;
            const scrollPane = this.sceneProxy.playBackLayer.getByName('scrollPane');
            const hSpace = Math.max(item.rect.height - item.content.rect.height, 0);
            const height = scrollPane.children.length * item.rect.height - hSpace;
            const y = rect.y + item.rect.y;
            if (height < rect.height) {
                scrollPane.y = y;
            } else {
                scrollPane.y = y - height + rect.height;
            }
        }
    },
    {
        /**
         * @static
         * @type {string}
         */
        NAME: 'PlaybackMediator'
    }
);