export default class ShanyiMenu {
    static showSyMenu() {
        !this.isShow && (this.isShow = true);
        const that = this;
        return new Promise((resolve, reject) => {
            const back = () => {
                document.body.removeChild(modalContainer);
                that.isShow = false;
                resolve(1);
            };
            const exit = (urlToGo) => {
                document.body.removeChild(modalContainer);
                that.isShow = false;
                resolve(urlToGo);
            };

            gameCallback.syMenuBack = back;
            gameCallback.syMenuExit = exit;

            const modalContainer = document.createElement('div');
            modalContainer.classList.add('syModalContainer');
            const modalPortal = document.createElement('div');
            modalPortal.classList.add('syModalPortal');
            if (document.body.clientWidth <= document.body.clientHeight) {
                modalPortal.style.transform = flashvars.vertical ? 'rotate(0)' : 'rotate(90deg)';
            }
            window.onresize = function(){
                if (document.body.clientWidth <= document.body.clientHeight) {
                    modalPortal.style.transform = flashvars.vertical ? 'rotate(0)' : 'rotate(90deg)';
                }
                else {
                    modalPortal.style.transform = flashvars.vertical ? 'rotate(90deg)' : 'rotate(0)';
                }
            }

            modalContainer.appendChild(modalPortal);

            const content = document.createElement('div');
            content.classList.add('syContent');
            modalPortal.appendChild(content);
            if(flashvars.vertical){
                //竖屏
                content.style.width = "320px";
                content.style.height = "510px";
            }

            const contentData = document.createElement('iframe');
            contentData.src = 'https://www.3000.com/popup.html?uuid=' + flashvars.uuid + "&wap=" + flashvars.wap + "&vertical=" + (flashvars.vertical ? 1 : 0);
            contentData.scrolling = 'no';
            contentData.width = '100%';
            contentData.height = '100%';
            contentData.frameBorder = '0';
            content.appendChild(contentData);


            document.body.appendChild(modalContainer);
        })
    }
}