import Utils from './Utils.js';

export default class FontUtils {
    static isFontExist(name){
        const baseFonts = ['Microsoft YaHei', 'Helvetica', 'sans-serif', 'serif'];
        const testString = "qwertyuiopasdfghjklzxcvbnm";
        const testSize = '72px';
        const s = document.createElement("span");
        s.style.fontSize = testSize;
        s.innerHTML = testString;
        if (!this.defaultWidth || !this.defaultHeight) {
            this.defaultWidth = {};
            this.defaultHeight = {};
            for (let index in baseFonts) {
                s.style.fontFamily = baseFonts[index];
                document.body.appendChild(s);
                this.defaultWidth[baseFonts[index]] = s.offsetWidth;
                this.defaultHeight[baseFonts[index]] = s.offsetHeight;
                document.body.removeChild(s);
            }
        }

        let detected = false;
        for (let index in baseFonts) {
            s.style.fontFamily = name + ',' + baseFonts[index];
            document.body.appendChild(s);
            var matched = (s.offsetWidth != this.defaultWidth[baseFonts[index]] || s.offsetHeight != this.defaultHeight[baseFonts[index]]);
            document.body.removeChild(s);
            detected = detected || matched;
        }

        return detected;
    }

    static mergeFontImage(font, default_font_size) {
        let images = [];
        if (font.size) {
            if (font.size.target.width) {
                this.charWidth = font.size.target.width;
                this.charHeight = font.size.target.height;
            }
            else {
                this.charWidth = font.size.target;
                this.charHeight = font.size.target;
            }
        }
        else {
            this.charWidth = default_font_size;
            this.charHeight = default_font_size;
        }
        this.widthImg = this.countPerRow * this.charWidth;
        this.heightImg = this.rowPerImg * this.charHeight;

        return new Promise((resolve, reject) => {
            if (!font) {
                return resolve();
            }
            //font.fontFamily = 'YouYuanY';
            if (this.isFontExist(font.fontFamily)) {
                return resolve();
            }

            FontUtils.fontFamily = font.fontFamily;
            for (let i=0; i<font.images.length; i++) {
                images[i] = 'https://apppic.3000test.com' + font.images[i].url;
                this.fontChars += font.images[i].chars;
            }

            // doMerge
            let c = document.createElement('canvas');
            let ctx = c.getContext('2d');
            let len = images.length;                    // 图片数
            let nRow = Math.ceil(this.fontChars.length / this.countPerRow);    // 文字行数

            c.width = this.widthImg;
            c.height = nRow * this.charHeight;
            //ctx.rect(0, 0, c.width, c.height);
            //ctx.fillStyle='#fff';
            //ctx.fill();

            const drawing = (n) => {
                if(n < len){
                    let img = new Image;
                    let srcHeight = n === len - 1 ? (nRow % this.rowPerImg) * this.charHeight : this.heightImg;
                    img.src = images[n];
                    img.onload = function(){
                        ctx.drawImage(img, 0, 0, FontUtils.widthImg, srcHeight, 0, n*FontUtils.heightImg, FontUtils.widthImg, srcHeight);
                        return drawing(n+1);
                    }
                }else{
                    //保存生成作品图片
                    let base64 = c.toDataURL("image/png", 1);
                    let blob = Utils.dataURLtoBlob(base64);
                    let blobUrl = blob ? URL.createObjectURL(blob) : base64;
                    this.fontImage = blobUrl;
                    return resolve();
                }
            };
            return drawing(0);
        });
    }

    // 每个字符宽度
    static getCharSpace(char, fontSize) {
        if (/[a-zA-Z0-9]/.test(char)) {
            return 14 * fontSize / 25;
        }
        else if ('(' === char || ')' === char) {
            return 8 * fontSize / 25;
        }
        else if ('-' === char) {
            return 11 * fontSize / 25;
        }
        else if ('.' === char || ':' === char) {
            return 7;
        }
        else if (32 === char.charCodeAt(0) || 160 === char.charCodeAt(0) || '\u2002' === char) { // 半角空格 == 半个汉字
            return fontSize >> 1;
        }
        else {
            return fontSize;
        }
    }

    static getLeftMargin(char, fontSize) {
        if (/[a-zA-Z0-9\.\)]/.test(char)) {
            return 0;
        }
        else if ('(' === char || ':' === char) {
            return -(4 * fontSize / 25);
        }
        else if ('-' === char) {
            return -(fontSize / 25);
        }
        return 6 * fontSize / 25;
    }
}

FontUtils.defaultWidth = undefined;
FontUtils.defaultHeight = undefined;
FontUtils.fontImage = undefined;
FontUtils.fontChars = '';
FontUtils.fontFamily = '';
FontUtils.charWidth = 25;   // 字宽
FontUtils.charHeight = 25;  // 字高
FontUtils.countPerRow = 40; // 一行字数
FontUtils.rowPerImg = 20;   // 每张图片行数
FontUtils.widthImg = 1000;  // 字体图片宽
FontUtils.heightImg = 500;  // 字体图片高