import puremvc from 'puremvc';
import FontUtils from '../../common/FontUtils.js';
import Utils from '../../common/Utils.js';
puremvc.define
(
    {
        name: 'diygamePlayer.view.mediator.SettingsMediator',
        parent: puremvc.Mediator
    },
    {
        listNotificationInterests: function () {
            return [
                diygamePlayer.AppConstants.SCENE_GOTO_SETTINGS
            ]
        },

        handleNotification: function (note) {
            const body = note.getBody();
            switch (note.getName()) {
                case diygamePlayer.AppConstants.SCENE_GOTO_SETTINGS:
                    this.callbackInfo = body.callbackInfo;
                    this.showSettingsLayer(body.context);
                    break;
                default:
                    break;
            }
        },

        onRegister: function () {
            this.sceneProxy = this.facade.retrieveProxy(diygamePlayer.model.proxy.SceneProxy.NAME);
            const sceneMediator = this.facade.retrieveMediator(diygamePlayer.view.mediator.SceneMediator.NAME);
            this.game = sceneMediator.viewComponent && sceneMediator.viewComponent.game;
            this.viewComponent = sceneMediator.viewComponent;
        },

        fullMode: function () {
            const {fullMode, windowMode} = this.sceneProxy.json_data.template.json.settings;
            const full = this.sceneProxy.settingsLayer.getByName('settings_button_fullMode');
            const window = this.sceneProxy.settingsLayer.getByName('settings_button_windowMode');
            full && full.changeBackground(fullMode.checked.background.url, fullMode.checked.text);
            window && window.changeBackground(windowMode.unchecked.background.url, windowMode.unchecked.text);
        },

        windowMode: function () {
            const {fullMode, windowMode} = this.sceneProxy.json_data.template.json.settings;
            const full = this.sceneProxy.settingsLayer.getByName('settings_button_fullMode');
            const window = this.sceneProxy.settingsLayer.getByName('settings_button_windowMode');
            full && full.changeBackground(fullMode.unchecked.background.url, fullMode.unchecked.text);
            window && window.changeBackground(windowMode.checked.background.url, windowMode.checked.text);
        },

        autoOn: function () {
            const {autoOn, autoOff} = this.sceneProxy.json_data.template.json.settings;
            const on = this.sceneProxy.settingsLayer.getByName('settings_button_autoOn');
            const off = this.sceneProxy.settingsLayer.getByName('settings_button_autoOff');
            on && on.changeBackground(autoOn.checked.background.url, autoOn.checked.text);
            off && off.changeBackground(autoOff.unchecked.background.url, autoOff.unchecked.text);
        },

        autoOff: function () {
            const {autoOn, autoOff} = this.sceneProxy.json_data.template.json.settings;
            const on = this.sceneProxy.settingsLayer.getByName('settings_button_autoOn');
            const off = this.sceneProxy.settingsLayer.getByName('settings_button_autoOff');
            on && on.changeBackground(autoOn.unchecked.background.url, autoOn.unchecked.text);
            off && off.changeBackground(autoOff.checked.background.url, autoOff.checked.text);
        },

        updateVolumeProgress(type,value){
            const progGroup = this.sceneProxy.settingsLayer.getByName(type + 'progGroup');
            if(progGroup){
                const pk = progGroup.getByName('pk');
                const progbk = progGroup.getByName('progbk');
                const totalwidth = progbk.width;
                if(value <= 0){pk.width = 0.01;}else{ //遮罩宽度为0时 遮罩效果无效
                    pk.width = value * totalwidth;
                }
            }

        },

        addSettings: function () {
            const {background, header, displayHeader, textHeader, displayCaption, textCaption, fullMode, windowMode, autoOn, autoOff, back} = this.sceneProxy.json_data.template.json.settings;

            //添加进度条
            const addProgress = (type,norkey,progkey,rc,callback)=>{
                const progGroup = this.game.add.group();
                progGroup.x = rc.x;
                progGroup.y = rc.y;
                progGroup.name = type + 'progGroup';
                this.sceneProxy.settingsLayer.add(progGroup);
                const normalkey = this.game.load.cache.checkImageKey(norkey) ? norkey : '';
                const progresskey = this.game.load.cache.checkImageKey(progkey) ? progkey : '';
                let progbk = this.game.add.image(0,0,normalkey,null,progGroup);
                progbk.width = rc.width;
                progbk.height = rc.height;
                progbk.name = 'progbk';
                let progpk = this.game.add.image(0,0,progresskey,null,progGroup);
                progpk.width = rc.width;
                progpk.height = rc.height;
                progpk.name = 'progpk';

                const pk = this.game.add.graphics(0,0);
                pk.beginFill(0xfff000,0.5);
                pk.drawRect(0, 0, rc.width, rc.height);
                pk.endFill();
                pk.name = 'pk'
                progGroup.add(pk);
                progpk.mask = pk;

                let isProgDown = false;
                progbk.inputEnabled = true;
                const totalwidth = progbk.width; //总长度
                let progress = 0;
                this.game.time.events.stop(false);
                const update = (value)=>{
                    if(value <= 0){pk.width = 0.01;}else{ //遮罩宽度为0时 遮罩效果无效
                        pk.width = value * totalwidth;
                    }
                }
                progbk.events.onInputDown.add((obj,point)=>{
                    isProgDown = true;
                    progress = ((point.x - rc.x) / totalwidth)
                    update(progress)
                    this.game.time.events.start();
                },this);//按下
                this.game.time.events.loop(10,()=>{
                    if(isProgDown){
                        progress = ((this.game.input.x - rc.x) / totalwidth)
                        if(progress <=0) progress = 0;
                        if(progress >=1) progress = 1;
                        update(progress);
                    }
                },this);

                this.game.input.onUp.add(()=>{
                    if(isProgDown){
                        if(callback)callback(progress);
                        this.game.time.events.stop(false);
                    }
                    isProgDown = false;
                },this)//弹起
            }

            const addTextBk = (bkKey, rc, name) => {
                const key = this.game.load.cache.checkImageKey(bkKey) ? bkKey : '';
                const bk = this.sceneProxy.settingsLayer.create(rc.x, rc.y, key);
                bk.width = rc.width;
                bk.height = rc.height;
                bk.name = name;
            };

            const addTextContent = (x, y, width, height, content, objText, name) => {
                const fontStyle = {};
                let ff = '';
                ff += objText.fontWeight ? objText.fontWeight + ' ' : 'normal ';
                ff += objText.fontSize ? parseInt(objText.fontSize) + 'px ' : '';
                ff += this.sceneProxy.json_data.font ? this.sceneProxy.json_data.font.fontFamily : objText.fontFamily ? objText.fontFamily : '';

                fontStyle.font = ff;
                fontStyle.fill = objText.color;
                fontStyle.boundsAlignH = "center";
                fontStyle.boundsAlignV = "middle";
                fontStyle.stroke = objText.stroke;
                fontStyle.strokeThickness = objText.stroke ? 2 : 0;

                const txt = this.game.add.text(0, 0, content, fontStyle, this.sceneProxy.settingsLayer);
                txt.name = name;
                if (FontUtils.fontFamily) {
                    const colorS = objText.color ? objText.color : '#fefefe';
                    const fontSize = objText.fontSize ? parseInt(objText.fontSize) : 25;
                    let color = '';
                    if (colorS.indexOf('rgb') !== -1) {
                        const rgb = colorS.replace('rgb(', '').replace(')', '').split(',');
                        color = '0x' + parseInt(rgb[0], 16) + parseInt(rgb[1], 16) + parseInt(rgb[2], 16);
                        color === '0xffffff' && (color = '0xfefefe');
                    }
                    else {
                        color = (colorS === '#ffffff' ? '#fefefe' : colorS).replace('#', '0x');
                    }
                    const fh = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontHeight(fontSize) : fontSize;
                    const fw = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontWidth(fontSize) : fontSize;
                    txt.colors.splice(0, txt.colors.length);
                    txt.text = ' ';
                    txt.fill = color;
                    txt.addColor(color, 0);
                    txt.x = x + (width - fw * content.length) / 2;
                    txt.y = y + (height - fh) / 2;
                    txt.wordWrapWidth = fw * (content.length + 1);
                    this.sceneProxy.addImageFontText([{data:content}], fw, fh, txt, this.sceneProxy.context);
                }
                else {
                    txt.setTextBounds(x, y + (txt.height - parseInt(objText.fontSize)) / 2, width, height);
                }
                objText.hidden && (txt.visible = false);
                return txt;
            };

            const addButton = (obj, name, text, isCheckBox) => {
                const t = {};
                t.normal = isCheckBox ? obj.unchecked.text : obj.normal.text;
                t.active = isCheckBox ? null : obj.active.text;
                t.text = text;
                const btn = this.viewComponent.addButton(obj.rect.x,
                    obj.rect.y,
                    obj.rect.width,
                    obj.rect.height,
                    isCheckBox ? null : obj.normal.background.url,
                    isCheckBox ? null : obj.active.background.url,
                    modeBtnClicked,
                    t,
                    this.sceneProxy.btn_sound,
                    name,
                    this.sceneProxy.settingsLayer,
                    isCheckBox
                );
                isCheckBox && btn.changeBackground(obj.unchecked.background.url, text);
                return btn;
            };

            const modeBtnClicked = (argv, pointer) => {
                if (pointer.pointerMode === Phaser.PointerMode.CURSOR && pointer.button !== 0) {
                    return;
                }
                if (argv.parent.name === 'settings_button_fullMode') {
                    this.fullMode();
                    this.sceneProxy.onSettingsFullModeBtnClicked();
                }
                else if (argv.parent.name === 'settings_button_windowMode') {
                    this.windowMode();
                    this.sceneProxy.onSettingsWindowModeBtnClicked();
                }
                else if (argv.parent.name === 'settings_button_autoOn') {
                    this.autoOn();
                    this.sceneProxy.onSettingsAutoOnBtnClicked();
                }
                else if (argv.parent.name === 'settings_button_autoOff') {
                    this.autoOff();
                    this.sceneProxy.onSettingsAutoOffBtnClicked();
                }
                else if (argv.parent.name === 'settings_button_back') {
                    this.sceneProxy.isQuickPlay = true;
                    appCanBack(flashvars.client,flashvars.app_version,false,flashvars.offline === '1');
                    this.sceneProxy.onSettingsBackBtnClicked();
                    if (this.callbackInfo) {
                        this.callbackInfo.callbackFun(this.callbackInfo.callbackArgs);
                        this.callbackInfo = null;
                    }
                }
            };

            const bk = this.sceneProxy.settingsLayer.create(0, 0, this.game.load.cache.checkImageKey(background.url) && background.url);
            bk.width = this.game.world.width;
            bk.height = this.game.world.height;
            bk.name = 'settings_bk_main';
            bk.inputEnabled = true;

            // header
            addTextBk(header.background.url, header.rect);
            if (header.title.text.hidden != true) {
                addTextContent(header.title.rect.x
                    , header.title.rect.y
                    , header.title.rect.width
                    , header.title.rect.height
                    , this.sceneProxy.stringMap.settings
                    , header.title.text
                    , 'settings_text_header');
            }

            // displayHeader
            if(!displayHeader.hidden){
                addTextBk(displayHeader.background.url, displayHeader.rect);
                if (!displayHeader.title.text.hidden) {
                    addTextContent(displayHeader.title.rect.x + displayHeader.rect.x
                        , displayHeader.title.rect.y + displayHeader.rect.y
                        , displayHeader.title.rect.width
                        , displayHeader.title.rect.height
                        , this.sceneProxy.stringMap.displayHeader
                        , displayHeader.title.text
                        , 'settings_text_displayHeader');
                }
            }

            // textHeader
            if(!textHeader.hidden){
                addTextBk(textHeader.background.url, textHeader.rect);
                if (!textHeader.title.text.hidden) {
                    addTextContent(textHeader.title.rect.x + textHeader.rect.x
                        , textHeader.title.rect.y + textHeader.rect.y
                        , textHeader.title.rect.width
                        , textHeader.title.rect.height
                        , this.sceneProxy.stringMap.textHeader
                        , textHeader.title.text
                        , 'settings_text_textHeader');
                }
            }


            // displayCaption
            addTextBk(displayCaption.background.url, displayCaption.rect);
            if (!displayCaption.text.hidden) {
                addTextContent(displayCaption.rect.x
                    , displayCaption.rect.y
                    , displayCaption.rect.width
                    , displayCaption.rect.height
                    , this.sceneProxy.stringMap.displayCaption
                    , displayCaption.text
                    , 'settings_text_displayCaption');
            }

            // textCaption
            addTextBk(textCaption.background.url, textCaption.rect);
            if (!textCaption.text.hidden) {
                addTextContent(textCaption.rect.x
                    , textCaption.rect.y
                    , textCaption.rect.width
                    , textCaption.rect.height
                    , this.sceneProxy.stringMap.textCaption
                    , textCaption.text
                    , 'settings_text_textCaption');
            }

            // fullMode
            if (!fullMode.hidden) {
                addButton(fullMode, 'settings_button_fullMode', this.sceneProxy.stringMap.fullMode, true);
            }

            // window mode
            if (!windowMode.hidden) {
                addButton(windowMode, 'settings_button_windowMode', this.sceneProxy.stringMap.windowMode, true);
            }

            // autoOn
            if (!autoOn.hidden) {
                addButton(autoOn, 'settings_button_autoOn', this.sceneProxy.stringMap.autoOn, true);
            }

            // autoOff
            if (!autoOff.hidden) {
                addButton(autoOff, 'settings_button_autoOff', this.sceneProxy.stringMap.autoOff, true);
            }

            // back
            if (!back.hidden) {
                const btnGroup = addButton(back, 'settings_button_back', this.sceneProxy.stringMap.back);
                btnGroup.button.onInputUp.add(() => this.sceneProxy.btn_sound && this.sceneProxy.btn_sound.stop())
            }

            //musicCaption
            if(this.sceneProxy.json_data.template.json.settings.musicCaption){
                const musicCaption = this.sceneProxy.json_data.template.json.settings.musicCaption;
                addTextBk(musicCaption.background.url,musicCaption.rect);
                if (!musicCaption.text.hidden) {
                    addTextContent(musicCaption.rect.x
                        , musicCaption.rect.y
                        , musicCaption.rect.width
                        , musicCaption.rect.height
                        , this.sceneProxy.stringMap.musicCaption
                        , musicCaption.text
                        , 'settings_text_musicCaption');
                }
            }
            //musicVolume
            if(this.sceneProxy.json_data.template.json.settings.musicVolume){
                const musicVolume = this.sceneProxy.json_data.template.json.settings.musicVolume;
                addProgress('music',musicVolume.normal.background.url,musicVolume.progress.background.url,musicVolume.rect,(value)=>{
                    this.sceneProxy.updateMusicVolume(value);
                });
            }
            //soundCaption
            if(this.sceneProxy.json_data.template.json.settings.soundCaption){
                const soundCaption = this.sceneProxy.json_data.template.json.settings.soundCaption;
                addTextBk(soundCaption.background.url,soundCaption.rect);
                if (!soundCaption.text.hidden) {
                    addTextContent(soundCaption.rect.x
                        , soundCaption.rect.y
                        , soundCaption.rect.width
                        , soundCaption.rect.height
                        , this.sceneProxy.stringMap.soundCaption
                        , soundCaption.text
                        , 'settings_text_soundCaption');
                }
            }
            //soundVolume
            if(this.sceneProxy.json_data.template.json.settings.soundVolume){
                const soundVolume = this.sceneProxy.json_data.template.json.settings.soundVolume;
                addProgress('sound',soundVolume.normal.background.url,soundVolume.progress.background.url,soundVolume.rect,(value)=>{
                    this.sceneProxy.updateSoundVolume(value)
                });
            }
            //volumeHeader
            if(this.sceneProxy.json_data.template.json.settings.volumeHeader){
                const volumeHeader = this.sceneProxy.json_data.template.json.settings.volumeHeader;
                addTextBk(volumeHeader.background.url, volumeHeader.rect);
                if (!volumeHeader.title.text.hidden) {
                    addTextContent(displayHeader.title.rect.x + displayHeader.rect.x
                        , displayHeader.title.rect.y + displayHeader.rect.y
                        , displayHeader.title.rect.width
                        , displayHeader.title.rect.height
                        , this.sceneProxy.stringMap.volumeHeader
                        , displayHeader.title.text
                        , 'settings_text_displayHeader');
                }
            }

            this.sceneProxy.settingsLayer.visible = false;
        },

        showSettingsLayer: function (context) {
            if (this.sceneProxy.settingsLayer.children.length === 0) {
                this.addSettings();
            }
            const rectScreenMode = () => {
                if (this.game.scale.isFullScreen) {
                    this.fullMode();
                    context.fullScreen = true;
                } else {
                    this.windowMode();
                    context.fullScreen = false;
                }
            };
            const rectTextMode = () => {
                if (context.auto) {
                    this.autoOn();
                } else {
                    this.autoOff();
                }
            };
            if (!this.fullScreenHandler) {
                this.fullScreenHandler = this.game.scale.onFullScreenChange.add(e => rectScreenMode());
            }
            if(this.sceneProxy.json_data.template.json.settings.musicVolume || this.sceneProxy.json_data.template.json.settings.soundVolume){
                this.sceneProxy.isQuickPlay = false;
                appCanBack(flashvars.client,flashvars.app_version,true,flashvars.offline === '1');
            }
            rectScreenMode();
            rectTextMode();
            this.updateVolumeProgress('sound',Utils.soundVolume);
            this.updateVolumeProgress('music',Utils.musicVolume);
            this.sceneProxy.settingsLayer.visible = true;
            this.game.world.bringToTop(this.sceneProxy.settingsLayer);
        }
    },
    {
        /**
         * @static
         * @type {string}
         */
        NAME: 'SettingsMediator'
    }
);