/**
 * @class demo.controller.StartupCommand
 * @extends puremvc.MacroCommand
 */
import puremvc from 'puremvc';
puremvc.define
(
	// CLASS INFO
	{
		name: 'diygamePlayer.controller.command.StartupCommand',
		parent: puremvc.MacroCommand
	},

	// INSTANCE MEMBERS	
	{
		/** @override */
		initializeMacroCommand: function ()
		{
			// add the PrepareControllerCommand- it will register Commands with the Facade
			this.addSubCommand( diygamePlayer.controller.command.PrepareControllerCommand );

			// add the PrepareModelCommand- it will register Proxys with the Facade
			this.addSubCommand( diygamePlayer.controller.command.PrepareModelCommand );

			// add the SetupViewsCommand- it will register Mediators with the Facade
			this.addSubCommand( diygamePlayer.controller.command.PrepareViewCommand );
		}
	}
)