Utils.timeStamp = -1;        // 服务器时间戳
Utils.timeOffset = 0;       // 与服务器间时间偏移量
Utils.sensitiveWords = [];  // 敏感词
Utils.selOptions = [];      // 游戏经过的选项uuid
Utils.soundVolume = 1;      //音效大小百分比
Utils.musicVolume = 1;      //音乐大小百分比
Utils.localTime = -1;       //游戏开始时本地获取到的本地时间

class DelayRemoveManager {
    constructor(){
        this.gifList = [];
        this.imgList = [];
    }

    popGif() {
        const index = this.gifList.findIndex(c => ResourcesManager.getCount('img', c.key) === 1);
        if (~index) {
            const { key } = this.gifList[index];
            this.gifList.splice(index, 1);
            ResourcesManager.removeFromCache('img', key, true);
        }
    }

    popImg() {
        const index = this.imgList.findIndex(c => ResourcesManager.getCount('img', c.key) === 1);
        if (~index) {
            const { key } = this.imgList[index];
            this.imgList.splice(index, 1);
            ResourcesManager.removeFromCache('img', key, true);
        }
    }

    push(key, isGif) {
        if (isGif) {
            this.gifList.push({ key, timestamp: Date.now() });
            ResourcesManager.addRefCount('img', key, 'cache');
            if (this.gifList.length > 20) {
                this.popGif();
            }
        } else {
            this.imgList.push({ key, timestamp: Date.now() });
            ResourcesManager.addRefCount('img', key, 'cache');
            if (this.imgList.length > 100) {
                this.popImg();
            }
        }
    }
}


export class ResourcesManager {
    static pushInLoader(url, type, key, isPlot) {
        const sKey = key || url;
        if (('img' === type && this.cache.checkImageKey(sKey) ||
                'audio' === type && this.cache.checkSoundKey(sKey)) &&
            !('ios' === flashvars.client && 'audio' === type && flashvars.app_version >= '1.5.0')) {
            return Promise.resolve(false);
        }
        return Utils.convertToAllowAccessControlOriginUrl(url).then(url => {
            if (!url) {
                return false;
            }
            const noLoading = !this.load.getAsset('img' === type ? 'image' : 'audio', sKey);
            if ('img' === type) {
                noLoading && this.load.image(sKey, url);
                !isPlot && this.addRefCount("img", sKey, 'global');
            } else if ('audio' === type) {
                let bAutoDecode = this.forceDecode.music === sKey || this.forceDecode.sounds.some(k => k === sKey);
                if ('ios' === flashvars.client && flashvars.app_version >= '1.5.0') {
                    const noCache = !PhoneUtils.isAudioInCache(sKey) && !PhoneUtils.isLoadingAudio(sKey);
                    if (noCache) {
                        PhoneUtils.loadAudioUrl(sKey, url, flashvars.offline);
                    }
                    return noCache;
                } else {
                    noLoading && this.load.audio(sKey, url, bAutoDecode);
                }
                !isPlot && this.addRefCount("audio", sKey, 'global');
            }
            return noLoading;            
        }, e => console.error("Invalid URL: " + e))
    }

    static forEachReCount(fun) {
        Object.keys(this.refCount.audio).forEach(key => fun && fun('audio', key, this.getCount('audio', key)));
        Object.keys(this.refCount.img).forEach(key => fun && fun('img', key, this.getCount('img', key)));
    }

    static resetReCount(filterKeys=[]) {
        [this.refCount.img, this.refCount.audio].forEach((obj, i) => {
            Object.keys(obj).forEach(key => {
                const oldData = obj[key];
                const { global=0, cache=0 } = oldData;
                const newData = { global, cache };
                const keys = Object.keys(obj[key]);
                filterKeys.forEach(key => keys.forEach(k => ~k.indexOf(key) ? newData[k] = oldData[k] : void 0));
                obj[key] = newData;
            });
        });
    }

    static updateRefTypePrefix(prefix, keep = true, remove = true) {
        [this.refCount.img, this.refCount.audio].forEach((obj, i) => {
            Object.keys(obj).forEach(key => {
                if(!obj.hasOwnProperty(key)){
                    return;
                }
                Object.keys(obj[key]).forEach(k => {
                    if (k.startsWith(prefix)) {
                        const refCount = k.replace(prefix, '');
                        keep && refCount && (obj[key][refCount] = (obj[key][refCount] || 0)+(obj[key][k] || 0));
                        delete obj[key][k];
                        remove && this.removeFromCache(i ? 'audio' : 'img', key);
                    }
                });
            });
        });
    }

    static transferRefCount(type, fKey, fRefType, tKey, tRefType) {
        this.refCount[type][tKey] = this.refCount[type][tKey] || {};
        this.refCount[type][tKey][tRefType] = this.refCount[type][tKey][tRefType] || 0;
        if (!this.refCount[type][fKey]) {
            return;
        }
        this.refCount[type][tKey][tRefType] += this.refCount[type][fKey][fRefType];
        this.refCount[type][fKey][fRefType] = 0;
        this.removeFromCache(type, fKey);
    }

    static transferByRefCount(fRefType, tRefType) {
        [this.refCount.img, this.refCount.audio].forEach(obj => {
            Object.keys(obj).forEach(key => {
                const refCount = obj[key];
                refCount[tRefType] = (refCount[tRefType] || 0)+(refCount[fRefType] || 0);
                delete refCount[fRefType];
            });
        });
    }

    static transferByRefCountByPrefix(prefix, refType) {
        [this.refCount.img, this.refCount.audio].forEach(obj => {
            Object.keys(obj).forEach(key => {
                const refCount = obj[key];
                let count = 0;
                Object.keys(refCount).forEach(k => {
                    if (k.startsWith(prefix)) {
                        count += refCount[k]
                        delete refCount[k];
                    }
                });
                refCount[refType] = (refCount[refType] || 0) + count;
            });
        });
    }
    
    static addRefCount(type, key, refType='play', count=1) {
        if (!type || !key) {
            return;
        }
        this.load.cancelForceRemoveFile(type, key);
        this.refCount[type][key] = this.refCount[type][key] || {};
        this.refCount[type][key][refType] = (this.refCount[type][key][refType] || 0) + count;
    }

    static subRefCount(type, key, refType='play') {
        if (this.refCount[type][key]) {
            const keys = Object.keys(this.refCount[type][key]);
            for (let i = 0; i < keys.length; i++) {
                if (/^\$([A-Za-z0-9]*-){4}([A-Za-z0-9]*)l\d/.test(keys[i])) {
                    return;
                }
            }
            const refCount = this.refCount[type][key];
            refCount[refType] = (refCount[refType] || 0) - 1;
            refCount[refType] = refCount[refType] < 0 ? 0 : refCount[refType];
            this.removeFromCache(type, key);
        }
        else if ('audio' === type && 'ios' === flashvars.client && '1.5.0' <= flashvars.app_version) {
            !!key && PhoneUtils.removeAudioUrl(key);
        }
    }

    static delRefTypeCount(type, key, refType) {
        if (this.refCount[type][key]) {
            const refCount = this.refCount[type][key];
            refCount[refType] = 0;
            this.removeFromCache(type, key);
        }
    }

    static delByRefType(refType){
        [this.refCount.img, this.refCount.audio].forEach((obj, i) => {
            Object.keys(obj).forEach(key => {
                delete obj[key];
                this.removeFromCache(i ? 'audio' : 'img', key);
            });
        });
    }

    static getCount(type, key, refType = '') {
        return this.refCount[type][key]
            ? (refType ? (this.refCount[type][key][refType] || 0) : Object.keys(this.refCount[type][key]).reduce((acc, cur) => acc + this.refCount[type][key][cur], 0))
            : 0;
    }

    static isGlobal(type, key) {
        return this.refCount[type][key]['global'] > 0;
    }

    static removeFromCache(type, key, force = false) {
        if (!this.refCount[type][key]) {
            return;
        }
       
        if (this.getCount(type, key) && !force) {
            return;
        }

        const { background, music, sounds, preBackground } = this.usingKeyMap;
        if (background === key || preBackground === key || music === key) {
            this.addRefCount(type, key);
            return;
        }
        for (let i = 0; i < sounds.length; i++) {
            if (sounds[i] === key) {
                this.addRefCount(type, key);
                return;
            }
        }

        if (type === 'audio' && this.cache) {
            if ('ios' === flashvars.client && flashvars.app_version >= '1.5.0') {
                PhoneUtils.removeAudioUrl(key);
            }
            else {
                const sound = this.cache.getSound(key);
                if (sound && sound.audioTag && sound.data) {
                    sound.data.free = true;
                }
                if (sound && /blob:/i.test(sound.url)) {
                    URL.revokeObjectURL(sound.url);
                }
                this.cache.checkSoundKey(key) && this.cache.removeSound(key);
                this.load.forceRemoveFile(type, key);
            }
            this.PreloadState.size += 2000000;
        }

        if (type === 'img' && this.cache) {
            if (key.indexOf("?") >= 0){
                key = key.substr(0, key.indexOf("?"));
            }
            const extension = key.substr((Math.max(0, key.lastIndexOf(".")) || Infinity) + 1).toLowerCase();
            if (!force) {
                this.delayRemoveManager.push(key, extension === 'gif');
                this.PreloadState.size += 200000;
                this.startPreloadResources();
                return;
            }
            const image = this.cache.getImage(key, true);
            if(image){
                const { frames } = image.data;
                frames && frames.forEach((frame) => {
                    URL.revokeObjectURL(frame.blobUrl);
                    frame.blobUrl = null;
                });
                /blob:/i.test(image.url) && URL.revokeObjectURL(image.url);
                this.cache.checkImageKey(key) && this.cache.removeImage(key);
                this.load.forceRemoveFile('image', key);
            }
            this.PreloadState.size += 200000;
        }
        delete this.refCount[type][key];
        this.startPreloadResources();
    }

    static startPreloadResources() {
        if (this.PreloadState.loading || this.PreloadState.size <= 0) {
            return;
        }
        this.PreloadState.loading = true;

        const createhMark = ({ context, chapter, pIndex, cmdInfo, cIndex, preloadType, preloadedCount, lock }) => {
            const { ...c } = context;
            c.stack = [...(c.stack || [])];
            c.cmdStack = [...(c.cmdStack || [])];
            return { context: c, chapter, pIndex, cmdInfo, cIndex, preloadType, preloadedCount, lock };
        };

        const getOptinsMarks = (plot, type, mark) => {
            const { context, chapter, pIndex = 0, cmdInfo, cIndex = 0, preloadType } = mark;
            let marks = [];
            const options = plot[type] || [];
            for(let i=0; i< options.length; i++){
                const option = options[i];
                if(!option.plots.length){
                    continue;
                }
                let temp = createhMark(mark);
                temp.chapter = {plots: option.plots, uuid: chapter.uuid};;
                temp.pIndex = 0;
                const t = type === 'options' ? 'o' : (option.type !== 'branch' && this.checkCondOptionResources(option) <= 50 ? 'l' : 'c');
                temp.preloadType = `${preloadType==='play'?'':preloadType}$${plot.uuid}${t}${i}-`;
                if(option.plots[0].commands){
                    temp.cmdInfo = { commands: option.plots[0].commands };
                    temp.cIndex = 0;
                }
                marks.push(temp);
            }
            return marks;
        };

        const getCmdOptinsMarks = (cmd, mark) => {
            const { context, chapter, pIndex = 0, cmdInfo, cIndex = 0, preloadType } = mark;
            let marks = [];
            const options = cmd.args;
            for(let i=0; i< options.length; i++){
                const option = options[i];
                if(!option.commands.length){
                    continue;
                }
                let temp = createhMark(mark);
                temp.cmdInfo = { commands: option.commands, parentInfo: cmdInfo, parentCmdIndex: cIndex };
                temp.cIndex = 0;
                let t = cmd.type === 'options' ? 'o' : (option.type !== 'branch' && this.checkCondOptionResources(option, 'command') <= 50 ? 'l' : 'c');
                temp.preloadType = `${preloadType==='play'?'':preloadType}$${cmd.uuid}${t}${i}-`;
                marks.push(temp);
            }
            return marks;
        };

        const updateMarkCmdInfo = (mark) => {
            if (!mark.cmdInfo && mark.chapter.plots.length > mark.pIndex && mark.chapter.plots[mark.pIndex].commands) {
                mark.cmdInfo = { commands: mark.chapter.plots[mark.pIndex].commands };
                mark.cIndex = 0;
            }
        };

        const updateMark = (mark, type, data) => {
            mark = createhMark(mark);
            let { context, chapter, cmdInfo, preloadType, pIndex=0, cIndex=0 } = mark;
            let newChapter = null;
            let temp = null;
            switch(type){
                case 'pop':
                    temp = context.stack[0];
                    context.stack = context.stack.slice(1);
                    mark.chapter = chapter.parent.pChapter;
                    mark.pIndex = chapter.parent.pnPlot + 1;
                    mark.preloadType = 'play';
                    mark.cmdInfo = null;
                    if(temp && temp.type === 'insert'){
                        context.cmdStack = context.cmdStack.slice(1);
                        mark.cmdInfo = temp.cmdInfo;
                        mark.cIndex = temp.cmdIndex + 1; 
                        mark.preloadType = temp.preloadType || 'play';
                        mark.pIndex--;
                    }
                    updateMarkCmdInfo(mark);
                    break;
                case 'jump':
                case 'command-jump':
                    context._preMark = { stack:[], cmdStack:[], chapter, cmdInfo, pIndex, cIndex, preloadType };
                    context.stack = [];
                    context.cmdStack = [];
                    mark.cmdInfo = null;
                    mark.chapter = Utils.searchChapterByUUID(this.gameData, data);
                    mark.pIndex = 0;
                    mark.preloadType = `${preloadType==='play'?'':preloadType}$${mark.chapter.uuid}j`;
                    updateMarkCmdInfo(mark);
                    break;
                case 'insert':
                case 'command-insert':
                    context._preMark = { stack:context.stack, cmdStack:context.cmdStack, chapter, cmdInfo, pIndex, cIndex, preloadType };
                    context.stack = [];
                    context.cmdStack = [];
                    mark.cmdInfo = null;
                    mark.chapter = Utils.searchChapterByUUID(this.gameData, data);
                    mark.pIndex = 0;
                    mark.preloadType = `${preloadType==='play'?'':preloadType}$${mark.chapter.uuid}i`;
                    updateMarkCmdInfo(mark);
                    break;
                case 'command-pop':
                    mark.cmdInfo = cmdInfo && cmdInfo.parentInfo || null;
                    mark.cIndex = (cmdInfo && cmdInfo.parentCmdIndex || 0) + 1;
                    if (cmdInfo.parentInfo) {
                        mark.preloadType = 'play';
                        context.cmdStack = context.cmdStack.slice(1);
                    } else {
                        mark.cmdInfo = null;
                        mark.pIndex++;
                        updateMarkCmdInfo(mark);
                    }
                    break;
                case 'command-break':
                    mark.cmdInfo = null;
                    mark.pIndex++;
                    updateMarkCmdInfo(mark);
                    break;
            }
            return mark;
        }

        const updateLoadedMarks = () => {
            const specialTypes = ['jump', 'insert', 'back', 'options', 'conditionOptions', 'addImage', 'showDialog', 'playMusic', 'playSound', 'weather'];
            for (let j = 0, len = this.LoadedMark.length; this.LoadedMark && j < len; j++) {
                for (let i = 0; this.LoadedMark[j] && i < this.LoadedMark[j].length; i++) {
                    let mark = this.LoadedMark[j][i];
                    let { context, chapter, pIndex = 0, cmdInfo, cIndex = 0, preloadType } = mark;

                    const weight = isNoUrgentlyMark(mark);
                    if(weight){
                        let optionCount = 0;
                        if (cmdInfo) {
                            const cmd = cmdInfo.commands[cIndex] || {};
                            const { type, args } = cmd;
                            optionCount = type === 'options' || type === 'conditionOptions' ? args.length : 0;
                        } else {
                            const plot = chapter.plots[pIndex] || {};
                            const { options=[], condition_options=[] } = plot;
                            optionCount = Math.max(options.length, condition_options.length);
                        }
                      
                        if(optionCount>5){
                           // console.log(1);
                        }
                        mark.preloadedCount = mark.preloadedCount || 0;
                        if (mark.preloadedCount > 10 || (weight === 2 && mark.preloadedCount > 1) || optionCount > 8) {
                            mark.lock = true;
                            continue;
                        } else {
                            mark.preloadedCount++;
                        }
                    }

                    if (mark.lock) {
                        mark.lock = false;
                        mark.preloadedCount = 0;
                        continue;
                    }

                    if (cmdInfo) {
                        if (cmdInfo.commands.length <= cIndex) {
                            this.LoadedMark[j].splice(i--, 1);
                            if ((context.stack.length || context.cmdStack.length) && cmdInfo.parentInfo) {
                                this.pushLoadMarks([updateMark(mark, 'command-pop')]);
                            } else if (!cmdInfo.parentInfo) {
                                this.pushLoadMarks([updateMark(mark, 'command-break')]);
                            }
                        } else {
                            const command = cmdInfo.commands[cIndex] || {};
                            const { type, args } = command;
                            if (type === 'options' || type === 'conditionOptions') {
                                const marks = getCmdOptinsMarks(command, mark);
                                mark.cIndex++;
                                marks.length && this.pushLoadMarks(marks);
                            } else if (type === 'back' || type === 'jump' || type === 'insert') {
                                if (type !== 'insert') {
                                    this.LoadedMark[j].splice(i--, 1);
                                }
                                type === 'jump' && this.pushLoadMarks([updateMark(mark, `command-jump`, args[0])]);
                                type === 'insert' && this.pushLoadMarks([updateMark(mark, `command-insert`, args[0])]);
                                mark.cIndex++;
                            } else {
                                let { type } = cmdInfo.commands[++mark.cIndex] || {};
                                while (type && !specialTypes.some(t => t === type)) {
                                    ({ type } = cmdInfo.commands[++mark.cIndex] || {});
                                }
                            }
                        }
                    } else if (chapter.plots.length <= pIndex) {
                        this.LoadedMark[j].splice(i--, 1);
                        if (context.stack.length) {
                            mark = updateMark(mark, 'pop');
                            this.pushLoadMarks([mark]);
                        }
                    } else {
                        const plot = chapter.plots[pIndex] || {};
                        const { options=[], condition_options=[] } = plot;
                        if (options.length || condition_options.length) {
                            const oMarks = getOptinsMarks(plot, 'options', mark);
                            const cMarks = getOptinsMarks(plot, 'condition_options', mark);

                            mark.pIndex++;
                            updateMarkCmdInfo(mark);

                            oMarks.length && this.pushLoadMarks(oMarks);
                            cMarks.length && this.pushLoadMarks(cMarks);
                        } else if(plot.back || plot.jump || plot.insert){
                            if (!plot.insert) {
                                this.LoadedMark[j].splice(i--, 1);
                            }
                            plot.jump && this.pushLoadMarks([updateMark(mark, 'jump', plot.jump)]);
                            plot.insert && this.pushLoadMarks([updateMark(mark, 'insert', plot.insert)]);
                            mark.pIndex++;
                            updateMarkCmdInfo(mark);
                        } else {   
                            mark.pIndex++;
                            updateMarkCmdInfo(mark);
                        }
                    }
                }
            }
            for (let i = 0; this.LoadedMark && i < this.LoadedMark.length; i++) {
                if(this.LoadedMark[i] && !this.LoadedMark[i].length){
                    this.LoadedMark.splice(i--, 1);
                }
            }
        };

        const revertMark = mark => {
            if(mark.context._preMark){
                let { stack, cmdStack, chapter } = mark.context._preMark;
                mark.context.stack = stack;
                mark.context.cmdStack = cmdStack;
                mark.chapter = chapter;
                mark.pIndex = 0;
                updateMarkCmdInfo(mark);
                delete mark.context._preMark;
            }
        };

        const loadChapter = mark => {
            const { chapter } = mark;
            if (chapter.md5 && chapter.plots.length === 0 && flashvars.offline !== '1' && !chapter.chapter) {//非离线，非付费章节
                const chapterUuid = chapter.downloadUuid ? chapter.downloadUuid : chapter.uuid;
                let loadChapterPromise = null;
                const handle = ({ type, plots=[] }) => {
                    if (type === 'revert') {
                        revertMark(mark);
                    } else if (type === 'succes') {
                        chapter.plots.length === 0 && chapter.plots.push(...plots);
                        this.PreloadState.loadingChapterMap[chapterUuid] = null;
                    }
                    return mark;
                };
                if(this.PreloadState.loadingChapterMap[chapterUuid]){
                    return this.PreloadState.loadingChapterMap[chapterUuid].then(handle);
                }
                loadChapterPromise = this.sceneProxy.checkIfChapterFree(chapterUuid, chapter.md5, false).then(
                    (plots=[]) => {
                        return {type:'succes', plots};
                    }, (msg) => {
                        console.warn(msg);
                    });
                this.PreloadState.loadingChapterMap[chapterUuid] = loadChapterPromise;
                return loadChapterPromise.then(handle);
            }
            return Promise.resolve(mark);
        };
        
        const isNoUrgentlyMark = mark => {
            let { preloadType='play' } = mark;
            const types = preloadType.split('$').slice(1).map(s => {
                const t = s.slice(36);
                if (/j$/.test(t)) {
                    return 'jump';
                } else if (/i$/.test(t)) {
                    return 'insert';
                } else if (/o\d-$/.test(t)) {
                    return 'option';
                } else if (/(c|l)\d-$/.test(t)) {
                    return 'condOption';
                }
            });
            let optionIndexs = [];
            let condOptionIndexs = [];
            let gotoIndexs = [];
            let result = 0;
            for (let i = 0; i < types.length; i++) {
                switch (types[i]) {
                    case 'option':
                        optionIndexs.push(i);
                        if (gotoIndexs.length) {
                            return 2;
                        }
                        if (optionIndexs.length > 2) {
                            return 2;
                        } else if (optionIndexs.length === 2) {
                            result = 1;
                        }
                        break;
                    case 'condOption':
                        condOptionIndexs.push(i);
                        if (condOptionIndexs.length > 2 && gotoIndexs.length >= 1) {
                            return 2;
                        }
                        if(condOptionIndexs.length > 2){
                            result = 1;
                        }
                        break;
                    case 'jump':
                    case 'insert':
                        gotoIndexs.push(i);
                        result = 1;
                        if (optionIndexs.length > 1) {
                            return 2;
                        }
                        if (gotoIndexs.length > 1) {
                            return 2;
                        }
                        break;
                }
                if (gotoIndexs.length && condOptionIndexs.length + optionIndexs.length > 2) {
                    return 2;
                }
                if (i === 4) {
                    return 1;
                } else if (i > 4) {
                    return 2;
                }
            }
            return result;
        }

        const preloadResources = () => {
            updateLoadedMarks();
            const promises = [];
            let loadedCount = 0;
            for (let i = 0; this.LoadedMark && i < this.LoadedMark.length && this.PreloadState.preloaded < 200; i++) {
                const markPromises = [];
                for (let j = 0; this.LoadedMark[i] && j < this.LoadedMark[i].length; j++) {
                    const mark = this.LoadedMark[i][j];
                    if(mark.lock){
                        continue;
                    }
                    const promise = Utils.timeout(0)
                        .then(() => this.LoadedMark[i] && this.LoadedMark[i][j] && mark === this.LoadedMark[i][j] ? loadChapter(this.LoadedMark[i][j]) : Promise.reject())
                        .then(mark => this.preloadResources(mark))
                        .then(rs => Promise.all(rs.data.map(d => this.pushInLoader(d.key, d.type, d.key, true).then(r => Object.assign(d, {pushed: r})))))
                        .then(rs => {
                            const pushedList = rs.filter(r => r.pushed);
                            const size = pushedList.reduce((acc, cur) => acc + (cur.type === 'img' ? 200000 : 2000000), 0);
                            this.PreloadState.size -= size;
                            if (pushedList.length && size) {
                                loadedCount++;
                            }
                            pushedList.length && this.load.start();
                        }).then(() => Utils.timeout(15));
                    markPromises.push(promise);      
                }

                markPromises.length && promises.push(Promise.all(markPromises).then(null, r => console.warn(r)));
            }
            const finish = () => {
                loadedCount && this.PreloadState.preloaded++;
                if (this.PreloadState.size <= 0 || !this.LoadedMark.length || this.PreloadState.preloaded >= 200) {
                    this.PreloadState.loading = false;
                } else {
                    setTimeout(preloadResources, 0);
                }
            };
            promises.length ? Promise.all(promises).then(finish, finish) : (this.PreloadState.loading = false);
        };

       setTimeout(preloadResources, 0);
    }

    static resetInsertPreload(chapter) {
        const preloadType = `$${chapter.uuid}i`;
        this.transferByRefCount(preloadType, 'play');
        this.updateRefTypePrefix(preloadType);
        this.updateLoadMarkByPrefix(preloadType);
        this.startPreloadResources();
    }

    static resetInsertBack(context, chapter, pIndex=0, cmdPlotSnap = {}) {
        const { cmdInfo, cmdIndex=0 } = cmdPlotSnap
        ResourcesManager.LoadedMark = [];
        this.resetReCount();
        const usingKeyMap = this.sceneProxy.usingResourceCountMap();
        this.forEachReCount((type, key, count) => {
            if(usingKeyMap[key]){
                this.addRefCount(type, key, usingKeyMap[key]);
            }
        });
        this.getResourcesByContext(context, chapter, pIndex, cmdInfo, cmdIndex).then(() => this.startPreloadResources()); 
    }

    static resetJumpPreload(chapter) {
        const preloadType = `$${chapter.uuid}j`;
        this.resetReCount([preloadType]);
        this.transferByRefCount(preloadType, 'play');
        this.updateRefTypePrefix(preloadType, true, false);
        const usingKeyMap = this.sceneProxy.usingResourceCountMap();
        this.forEachReCount((type, key, count) => {
            if(usingKeyMap[key]){
                this.addRefCount(type, key, usingKeyMap[key]);
            }else if(count === 0){
                this.removeFromCache(type, key);
            }
        });
        this.updateLoadMarkByPrefix(preloadType, false);
        this.startPreloadResources();
    }

    static resetCmdPOptionPreload(commands, index, opIndex) {
        const cmd = commands[index];
        if(!cmd){
            return;
        }
        const t = cmd.type === 'options' ? 'o' : 'c';
        const preloadType = `$${cmd.uuid}${t}${opIndex}-`;

        const optionMarks = [];
        for (let i = 0; i < this.LoadedMark.length; i++) {
            for (let j = 0; j < this.LoadedMark[i].length; j++) {
                const mark = this.LoadedMark[i][j];
                if (mark.preloadType.startsWith(preloadType)) {
                    optionMarks.push(mark);
                }
            }
        }
        const nextJump = optionMarks.length && optionMarks.every(mark => /^\$.{36}j/.test(mark.preloadType.substring(preloadType.length)));
        this.transferByRefCount(preloadType, 'play');
        cmd.args.forEach((options, i) => {
            const prefix = `$${cmd.uuid}${t}${i}-`;
            const isSame = i === opIndex;
            !isSame && this.updateRefTypePrefix(prefix);
            !isSame && this.clearLoadMarkByPrefix(prefix);
        });
        this.updateLoadMarkByPrefix(preloadType, !nextJump, false);
        this.updateRefTypePrefix(preloadType, true);
        this.startPreloadResources();
    }

    static clearPreCmdResources(commands, index) {
        this.startPreloadResources();
        const cmd = commands[index-1];
        const specialTypes = ['jump', 'insert', 'back', 'options', 'conditionOptions', 'addImage', 'showDialog', 'playMusic', 'playSound', 'weather'];
        if(cmd && specialTypes.some(t => t === cmd.type)){
            this.PreloadState.preloaded--;
        }
        if(!cmd || cmd.type!== 'conditionOptions'){
            return;
        }
        cmd.args.forEach((options, i) => this.updateRefTypePrefix(`$${cmd.uuid}l${i}-`, false));
    }

    static resetPlotOptionPreload(plot, optionType='', index = -1) {
        const type = optionType === 'options' ? 'o' : (optionType === 'condition_options' ? 'c' : '');
        const { options=[], condition_options=[] } = plot;
        if (!options.length && !condition_options.length) {
            return;
        }
        const preloadType = `$${plot.uuid}${type}${index}-`;
        const optionMarks = [];
        for (let i = 0; i < this.LoadedMark.length; i++) {
            for (let j = 0; j < this.LoadedMark[i].length; j++) {
                const mark = this.LoadedMark[i][j];
                if (mark.preloadType.startsWith(preloadType)) {
                    optionMarks.push(mark);
                }
            }
        }
        this.transferByRefCount(preloadType, 'play');
        const nextJump = optionMarks.length && optionMarks.every(mark => /^\$.{36}j/.test(mark.preloadType.substring(preloadType.length)));
        options.forEach((options, i) => {
            const prefix = `$${plot.uuid}o${i}-`;
            const sameIndex = i === index && optionType === 'options';
            !sameIndex && this.updateRefTypePrefix(prefix, sameIndex);
            !sameIndex && this.clearLoadMarkByPrefix(prefix);
        });
        condition_options.forEach((options, i) => {
            const prefix = `$${plot.uuid}c${i}-`;
            const sameIndex = i === index && optionType === 'condition_options';
            !sameIndex && this.updateRefTypePrefix(prefix, sameIndex);
            !sameIndex && this.clearLoadMarkByPrefix(prefix);
        });
        this.updateLoadMarkByPrefix(preloadType, !nextJump, false);
        this.updateRefTypePrefix(preloadType, true);

        this.startPreloadResources();
    }

    static clearPrePlotResources(plots, index) {
        this.startPreloadResources();
        this.PreloadState.preloaded--;
        const plot = plots[index - 1];
        if (!plot || plot.commands ||  !plot.condition_options && !plot.condition_options.length) {
            return;
        }
        const { condition_options=[] } = plot;
        condition_options.forEach((options, i) => this.updateRefTypePrefix(`$${plot.uuid}l${i}-`, false));
     }

    static preloadResources(loadedMark){
        let {context, chapter, pIndex = 0, cmdInfo, cIndex =0, preloadType} = loadedMark;
        const resources = { data: [], size: 0 };
        if (!chapter || !chapter.plots || !chapter.plots[pIndex] || cmdInfo && !cmdInfo.commands[cIndex]) {
            return Promise.resolve(resources);
        }

        const addRefCount = (type, url, preloadType) => this.addRefCount(type, url, preloadType);

        const addResource = (url, type, preloadType) => {
            addRefCount(type, url, preloadType);
            if (!resources.data.some(d => d.key === url)) {
                return xhr.getResourceSize(url, type).then(size => {
                    resources.data.push({ key: url, type, size });
                    resources.size += size;
                    return true;
                });
            }
            return Promise.resolve(false);
        };
        const getInCmdInfo = (cmdInfo, cIndex, preloadType) => {
            if (cIndex >= cmdInfo.commands.length) {
                return Promise.resolve();
            }
            return Promise.all(this.getCommandResources(cmdInfo.commands[cIndex]).map(r => addResource(r.url, r.type, preloadType)));
        };

        const getInChapter = (chapter, pIndex ,preloadType) => {
            const promises = [];
            const plot = chapter.plots[pIndex];

            if (!plot || plot.commands && !plot.commands.length) {
                return Promise.resolve();
            }

            if(plot.commands){
                loadedMark.cmdInfo = { commands: plot.commands };
                loadedMark.cIndex = 0;
                return getInCmdInfo(plot, 0, preloadType);
            }

            return Promise.all(this.getPlotResources(plot).map(r => addResource(r.url, r.type, preloadType)));
        };

        return cmdInfo
            ? getInCmdInfo(cmdInfo, cIndex, preloadType).then(() => resources)
            : getInChapter(chapter, pIndex, preloadType).then(() => resources);
    }

    static pushLoadMarks(marks) {
        if (!marks.length) {
            return;
        }
        const revertMark = (mark, count) => {
            let { context, cmdInfo, cIndex, chapter, pIndex } = mark;
            context = { ...context, stack:[...(context.stack || [])], cmdStack: [...(context.cmdStack || [])] };
            while(count--){
                if (cmdInfo && cmdInfo.parentInfo) {
                    context.cmdStack.splice(0, 1);
                    cmdInfo = cmdInfo.parentInfo;
                    cIndex = cmdInfo.parentCmdIndex;
                    continue;
                } else {
                    cmdInfo = null;
                }
                if (context.stack.length) {
                    const frame = context.stack.splice(0, 1)[0];
                    if (frame && frame.type === 'insert') {
                        context.cmdStack.splice(0, 1);
                        cmdInfo = frame.cmdInfo;
                        cIndex = frame.cmdIndex + 1;
                    }
                    pIndex = chapter.parent.pnPlot;
                    chapter = chapter.parent.pChapter;
                } else {
                    break;
                }
            }
            return {context, cmdInfo, cIndex, chapter, pIndex, preloadType:'' };
        }

        const compareMark = (m1, m2) => {
            let { chapter, cmdInfo, preloadType, pIndex, cIndex, context } = m1;
            if (cmdInfo && m2.cmdInfo && cmdInfo.commands === m2.cmdInfo.commands && preloadType === m2.preloadType) {
                return cIndex - m2.cIndex;
            }
            if (chapter.plots === m2.chapter.plots && preloadType === m2.preloadType) {
                return pIndex - m2.pIndex;
            }

            if (chapter.uuid === m2.chapter.uuid && preloadType === m2.preloadType) {
                return pIndex - m2.pIndex;
            }

            if (m2.preloadType.startsWith(m1.preloadType)) {
                let m1Types = m1.preloadType.split('$').slice(1);
                let m2Types = m2.preloadType.split('$').slice(1);
                const sameCount = m2Types.length - m1Types.length;
                if (sameCount&&(m2.chapter.parent||m2.cmdInfo&&m2.cmdInfo.parentInfo)) {
                    return -compareMark(revertMark(m2, sameCount), revertMark(m1, 0));
                }
            }
            return NaN;
        }
        /* for (let i = 0; i < this.LoadedMark.length; i++) {
            for (let j = 0; j < this.LoadedMark[i].length; j++) {
                const result = compareMark(this.LoadedMark[i][j], marks[0]);
                if (result === 0 || result > 0) {
                   // return;
                }
            }
        } */

        this.LoadedMark.push(marks);
        //console.log(marks)
    }

    static updateLoadMarkByPrefix(prefix, keep=true, needPop=true) {
        for (let i = 0; i < this.LoadedMark.length; i++) {
            for (let j = 0; j < this.LoadedMark[i].length; j++) {
                const mark = this.LoadedMark[i][j];
                let { preloadType, context } = mark;
                if (preloadType.startsWith(prefix)) {
                    preloadType = preloadType.substring(prefix.length);
                    mark.preloadType = preloadType ? preloadType : 'play';
                    if (needPop) {
                        const frame = context.stack.pop();
                        if (frame && frame.type === 'insert') {
                            context.cmdStack.pop();
                        }
                    }
                } else if(!keep){
                    this.updateRefTypePrefix(preloadType, false);
                    this.LoadedMark[i].splice(j--, 1);
                }
            }
            if (!this.LoadedMark[i].length) {
                this.LoadedMark.splice(i--, 1)
            }
        }
    }

    static clearLoadMarkByPrefix(prefix) {
        for (let i = 0; i < this.LoadedMark.length; i++) {
            for (let j = 0; j < this.LoadedMark[i].length; j++) {
                const mark = this.LoadedMark[i][j];
                let { preloadType } = mark;
                if (preloadType.startsWith(prefix)) {
                    this.LoadedMark[i].splice(j--, 1);
                }
            }
            if (!this.LoadedMark[i].length) {
                this.LoadedMark.splice(i--, 1)
            }
        }
    }

    static getWeatherResources(type){
        const resources = [];
        if (type === 'snowy') {
            for (let i=0; i<120; i++) {
                resources.push({url: `/${Utils.getWeatherOfflinePathName()}/${Utils.getWeatherFilePathName('snow')}/xuehua_${Utils.formatNum(i)}.png`, type:'img'});
            }
        }
        else if(type === 'windy'){
            if(flashvars.vertical){
                for(let i =0;i < 20;i++){
                    resources.push({url: `/${Utils.getWeatherOfflinePathName()}/${Utils.getWeatherFilePathName("wind")}/feng_${Utils.formatNum(i)}.png`, type:'img'});
                }
            }
        }
        else if (type === 'rainy') {
            for (let i=0; i<50; i++) {
                resources.push({url: `/${Utils.getWeatherOfflinePathName()}/${Utils.getWeatherFilePathName('rain')}/xiayu_${Utils.formatNum(i)}.png`, type:'img'});
            }
        }
        else if (type === 'defoliation') {
            for (let i=0; i<90; i++) {
                resources.push({url: `/${Utils.getWeatherOfflinePathName()}/${Utils.getWeatherFilePathName('mapleLeaves')}/fengye_${Utils.formatNum(i)}.png`, type:'img'});
            }
        }
        else if (type === 'fallingPeach') {
            for (let i=0; i<90; i++) {
                resources.push({url: `/${Utils.getWeatherOfflinePathName()}/${Utils.getWeatherFilePathName('beachBlossom')}/taohua_${Utils.formatNum(i)}.png`, type:'img'});
            }
        }
        else if (type === 'fallingFlower') {
            for (let i=0; i<90; i++) {
                resources.push({url: `/${Utils.getWeatherOfflinePathName()}/${Utils.getWeatherFilePathName('blossom')}/yinghua_${Utils.formatNum(i)}.png`, type:'img'});
            }
        }
        else if (type === 'lightning') {
            for (let i=0; i<50; i++) {
                resources.push({url: `/${Utils.getWeatherOfflinePathName()}/${Utils.getWeatherFilePathName('lightning')}/shandian_${Utils.formatNum(i)}.png`, type:'img'});
            }
        }
        return resources;
    }

    static getCommandResources(command){
        const resources = [];
        switch (command.type) {
            case 'addImage':
                if(command.args[0].url && 'inherit' !== command.args[0].url){
                    resources.push({type:'img', url:command.args[0].url, isBackground: command.args[0].index === 1});
                }
                break;
            case 'showDialog':
                resources.push({type:'img', url:command.args[0].avatar.url});
                break;
            case 'playMusic':
                if(command.args[0].url && 'inherit'!== command.args[0].url){
                    resources.push({type:'audio', url:command.args[0].url, isMusic:true });
                }
                break;
            case 'playSound':
                if(command.args[0].url && 'inherit'!== command.args[0].url){
                    resources.push({type:'audio', url:command.args[0].url, isSound:true, index:command.args[0].index });
                }
                break;
            case 'options':
                for (let option of command.args) {
                    const { background, sound } = option;
                    if (background) {
                        const { normal, active, disable } = background;
                        normal && normal.url && resources.push({ type: 'img', url: normal.url });
                        active && active.url && resources.push({ type: 'img', url: active.url });
                        disable && disable.url && resources.push({ type: 'img', url: disable.url });
                    }
                    if (sound && sound.url) {
                        resources.push({ type: 'audio', url: sound.url });
                    }
                }
                break;
            case 'weather':
                if(flashvars.offline !== '1') resources.push(...this.getWeatherResources(command.args[0]));
                break;
            case 'showBubble':
                if(command.args[0].url){
                    resources.push({type:'img', url:command.args[0].url});
                }
                break;
            case 'emitBubble':
                const {avatar,content} = command.args[0];
                if(avatar.url){
                    resources.push({type:'img', url:avatar.url});
                }
                if(content.url){
                    resources.push({type:'img', url:content.url});
                }
                break;
            default:
                break;
        }
        return resources;
    }

    static getPlotResources(plot){
        const resources = [];
        // sound
        if (Object.prototype.toString.call(plot.plot_sound) == '[object Array]') {
            for (let i in plot.plot_sound) {
                let item = plot.plot_sound[i];
                if (item && 'inherit' !== item.url) {
                    resources.push({ url: item.url, type: 'audio', isSound:true, index: i });
                }
            }
        } else if (plot.plot_sound.url && 'inherit' !== plot.plot_sound.url) {
            resources.push({ url: item.url, type: 'audio', isSound:true, index: 0 });
        }
        // background
        if (plot.background.url && plot.background.url !== 'inherit') {
            resources.push({ url: plot.background.url, type: 'img', isBackground:true });
        }

        // avatar
        if (plot.avatar.url) {
            resources.push({ url: plot.avatar.url, type: 'img'});
        }
        // music
        if (plot.music.url && plot.music.url !== 'inherit') {
            resources.push({ url: plot.music.url, type:'audio', isMusic:true });
        } 

        // characters
        for (let character of plot.characters) {
            character.url && resources.push({ url: character.url, type: 'img' });
        }

        // props
        for (let prop of plot.props) {
            prop.url && resources.push({ url: prop.url, type: 'img' });
        }
        // options
        for (let option of plot.options) {
            const { background, sound } = option;
            if (background) {
                const { normal, active, disable } = background;
                normal && normal.url && resources.push({ type: 'img', url: normal.url });
                active && active.url && resources.push({ type: 'img', url: active.url });
                disable && disable.url && resources.push({ type: 'img', url: disable.url });
            }
            if (sound && sound.url) {
                resources.push({ type: 'audio', url: sound.url });
            }
        }
        // weather
        if (plot.weather.value) {
            if(flashvars.offline !== '1') resources.push(...this.getWeatherResources(plot.weather.value));
        }

        return resources;
    }

    static checkCondOptionResources(option, type = 'general'){
        let count = this.OptionResourcesCountMap.get(option) || 0;
        if (count) {
            return count;
        }
        const resourcesMap = {};

        const addResource = r => {
            if (!resourcesMap[r.url]) {
                count++;
                resourcesMap[r.url] = true;
            }
        };

        const getCountInOption = (option) => {
            for (let i = 0; i < option.plots.length && count <= 50; i++) {
                const plot = option.plots[i];
                let rs = [];
                if (plot.commands) {
                    rs = plot.commands.map(c => {
                        if (c.type === 'options' || c.type === 'conditionOptions') {
                            getCountInCmdOptions(c.args);
                        }
                        return this.getCommandResources(c);
                    }).reduce((acc, cur) => [...cur, acc], [])
                } else {
                    if (plot.options.length) {
                        getCountInOptions(plot.options);
                    }
                    if (plot.condition_options.length) {
                        getCountInOptions(plot.condition_options);
                    }
                    rs = this.getPlotResources(plot);
                }
                rs.forEach(r => addResource(r));
            }
        };
        const getCountInOptions = (options) => options.forEach(o => getCountInOption(o));

        const getCountInCmdOption = (option) => {
            for (let i = 0; i < option.commands.length && count <= 50; i++) {
                const cmd = option.commands[i];
                this.getCommandResources(cmd).forEach(r => addResource(r))
                if (cmd.type === 'options' || cmd.type === 'conditionOptions') {
                    getCountInCmdOptions(cmd.args);
                }
            }
        };
        const getCountInCmdOptions = (options) => options.forEach(o => getCountInCmdOption(o));

        if (type === 'general') {
            getCountInOption(option);
        } else {
            getCountInCmdOption(option);
        }

        return count;
    }

    static getResourcesByContext(context, chapter, pIndex = 0, cmdInfo, cIndex =0, canAddRefCount = true) {
        const resources = { data: [], size: 0 };
        context = { ...context };
        context.stack = [...(context.stack || [])];
        context.cmdStack = [...(context.cmdStack || [])];

        if (!chapter || !chapter.plots || !chapter.plots[pIndex] || cmdInfo && !cmdInfo.commands[cIndex]) {
            return Promise.resolve(resources);
        }

        const isMaxSize = () => resources.size > this.MaxSize/5 || resources.data.length > 30;

        const addRefCount = (type, url, preloadType) => canAddRefCount && this.addRefCount(type, url, preloadType);

        const addResource = (url, type, preloadType) => {
            addRefCount(type, url, preloadType);
            if (!resources.data.some(d => d.key === url)) {
                return xhr.getResourceSize(url, type).then(size => {
                    resources.data.push({ key: url, type, size });
                    resources.size += size;
                });
            }
            return Promise.resolve();
        };

        const createMark = ({context, chapter, pIndex, cmdInfo, cIndex, preloadType}) => {
            const c = { ...context, stack:[], cmdStack: [] };
            const plots = chapter.plots || [];
            if (!cmdInfo && plots.length > pIndex && plots[pIndex] && plots[pIndex].commands) {
                cmdInfo = { commands: plots[pIndex].commands };
                cIndex = -1;
            } 
            return { context: c, chapter, pIndex, cmdInfo, cIndex, preloadType  };
        }

        const createJumpMark = (content, chapterUuid) => {
            let { context, preloadType } = content;
            const newChapter = Utils.searchChapterByUUID(this.gameData, chapterUuid);
            if(!newChapter){
                return;
            }
            preloadType = `${preloadType==='play'?'':preloadType}$${newChapter.uuid}j`;
            content = { context, cmdInfo:null, chapter: newChapter, pIndex: -1 , preloadType};
            return createMark(content);
        }

        const createInsertMark = (content, chapterUuid) => {
            let { context, preloadType } = content;
            const newChapter = Utils.searchChapterByUUID(this.gameData, chapterUuid);
            if(!newChapter){
                return;
            }
            preloadType = `${preloadType==='play'?'':preloadType}$${newChapter.uuid}i`;
            content = { context, cmdInfo:null, chapter:newChapter, pIndex: -1 , preloadType };
            return createMark(content);
        }

        const getInCommand = (command, preloadType) => {
            const resources = this.getCommandResources(command);
            resources.forEach(r => {
                const {type, url, isMusic, isSound, index} = r;
                if (isMusic && this.forceDecode.musics.length < this.MaxForceDecodeMusic) {
                    this.forceDecode.musics.push(url);
                }
                if (isSound && !this.forceDecode.sounds[index]) {
                    this.forceDecode.sounds[index] = url;
                }
            });

            return Promise.all(resources.map(r => addResource(r.url, r.type, preloadType)));
        };

        const preloadCmdOptions = (content) => {
            const { context, preloadType, chapter, pIndex, cmdInfo, cIndex } = content;
            const cmd = cmdInfo.commands[cIndex];
            const options = cmd.args;
            const optionPromises = [];
            const marks = [];
            for (let opIndex = 0; options && opIndex < options.length; opIndex++) {
                const option = options[opIndex];
                let optionType = cmd.type === 'options' ? 'o' : (option.type !== 'branch' && this.checkCondOptionResources(option, 'command') <= 50 ? 'l' : 'c');
                const newCmdInfo = { commands: option.commands };
                const newpreloadType = `${preloadType==='play'?'':preloadType}$${cmd.uuid}${optionType}${opIndex}-`;
                const pushMark = (cIndex) => {
                    const mark = createMark({...content, cmdInfo:newCmdInfo, cIndex, preloadType:newpreloadType});
                    marks.push(mark);
                };
                const temp = { ...content, cmdInfo:newCmdInfo, cIndex:0, preloadType:newpreloadType }
                let promise = Promise.resolve();
                for (let opPlotIndex = 0; opPlotIndex < option.commands.length && opPlotIndex < 10; opPlotIndex++) {
                    const cmd = option.commands[opPlotIndex];
                    const {type, args} = cmd;
                    promise = promise.then(() => {
                        return getInCommand(cmd, newpreloadType).then(() => {
                            if (isMaxSize(true)) {
                                return Promise.reject(opPlotIndex);
                            } else if (type === 'options' || type === 'conditionOptions') {
                                const content = { ...temp, cIndex:opPlotIndex };
                                content.context = { ...context, cmdStack:[] };
                                return preloadCmdOptions(content);
                            }else if(type === 'jump'){
                                this.pushLoadMarks([createJumpMark({ ...temp, cIndex:opPlotIndex }, args[0])]);
                                return Promise.reject(-1);
                            } else if(type === 'insert'){
                                this.pushLoadMarks([createInsertMark({ ...temp, cIndex:opPlotIndex }, args[0])]);
                                return Promise.reject(-1);
                            } else if (type === 'back') {
                                return Promise.reject(-1);
                            } 
                            return Promise.resolve();
                        });
                    });
                    if (type === 'options' || type === 'conditionOptions') {
                        if(opPlotIndex + 1 < option.commands.length){
                            promise = promise.then(() => Promise.reject(opPlotIndex));
                        }
                        break;
                    }
                }
                promise = promise.then(() => {
                    if (option.commands.length > 10) {
                        pushMark(9);
                    }
                }, index => {
                    if (index >= 0 && option.commands.length > index) {
                        pushMark(index);
                    }
                });
                optionPromises.push(promise);
            }
            return Promise.all(optionPromises).then(() => {
                marks.length && this.pushLoadMarks(marks);
                return Promise.resolve();
            });
        };

        const getInNextCommand = (content, nextPlot = true) => {
            let { context, chapter, pIndex, cmdInfo, cIndex, preloadType } = content;
            if (isMaxSize(true)) {
                this.pushLoadMarks([createMark(content)]);
                return Promise.resolve();
            }
            const { type, args }= cmdInfo.commands[cIndex] || {};
            if(type === 'jump'){
                this.pushLoadMarks([createJumpMark(content, args[0])]);
                return Promise.resolve();
            } else if(type === 'insert'){
                this.pushLoadMarks([createInsertMark(content, args[0])]);
                return getInCmdInfo({...content, cIndex: cIndex+1 });
            } else if (type === 'back') {
                return Promise.resolve();
            } else if (type === 'options' || type === 'conditionOptions') {
                return preloadCmdOptions(content, nextPlot).then(() => getInCmdInfo({...content, cIndex: cIndex+1 }, nextPlot));
            } else if (cmdInfo.commands.length > cIndex + 1) {
                return getInCmdInfo({...content, cIndex: cIndex+1 }, nextPlot);
            } else if (nextPlot && context.cmdStack.length) {
                context.cmdStack = context.cmdStack.splice(1);
                cIndex = cmdInfo.parentCmdIndex + 1;
                cmdInfo = cmdInfo.parentInfo;
                if (cmdInfo && Number.isInteger(cIndex)) {
                    return getInCmdInfo({ ...content, cmdInfo, cIndex }, nextPlot);
                }
            } else if (nextPlot && context.stack.length) {
                //context.stack = context.stack.splice(1);
                context.stack = context.stack.slice(1);
                if(chapter.parent){
                    pIndex = chapter.parent.pnPlot + 1;
                    chapter = chapter.parent.pChapter;
                }
                if (chapter && Number.isInteger(pIndex)) {
                    return getInChapter({ ...content, chapter, pIndex});
                }
            }
            return nextPlot ? getInChapter({...content, pIndex: pIndex + 1}) : Promise.resolve() ;
        };

        const getInCmdInfo = (content, nextPlot=true) => {
            const {chapter, pIndex, cmdInfo, cIndex, preloadType } = content;
            if (cIndex >= cmdInfo.commands.length) {
                return getInNextCommand(content, nextPlot);
            }

            return getInCommand(cmdInfo.commands[cIndex], preloadType).then(() => getInNextCommand(content, nextPlot));
        };

        const getPlotOptionCount = plot => {
            let count = 0;
            for (let opIndex = 0; plot.options && opIndex < plot.options.length; opIndex++) {
                count += plot.options[opIndex].plots.length;
            }

            for (let opIndex = 0; plot.condition_options && opIndex < plot.condition_options.length; opIndex++) {
                count += plot.condition_options[opIndex].plots.length;
            }
            return count;
        };

        const getInChapter = (content) => {
            const { chapter, pIndex, preloadType } = content;
            const plot = chapter.plots[pIndex];

            if (!plot || plot.commands && !plot.commands.length) {
                return Promise.resolve();
            }

            if(plot.commands){
                return getInCmdInfo({...content, cmdInfo:plot, cIndex:0});
            }

            const getInPlot = (plot, preloadType) => {
                const resources = this.getPlotResources(plot);
                resources.forEach(r => {
                    const {type, url, isMusic, isSound, index} = r;
                    if (isMusic && this.forceDecode.musics.length < this.MaxForceDecodeMusic) {
                        this.forceDecode.musics.push(url);
                    }
                    if (isSound && !this.forceDecode.sounds[index]) {
                        this.forceDecode.sounds[index] = url;
                    }
                });
                return Promise.all(resources.map(r => addResource(r.url, r.type, preloadType)));
            };

            const preloadPlotOptions = (content) => {
                const { chapter, pIndex, preloadType, context } = content;
                const plot = chapter.plots[pIndex];
                const opMarks = [];
                const copMarks = [];
                const optionPromises = [];
                const condOptionPromises = [];

                const pushMark = (content, marks) => marks.push(createMark(content));
                const loadPlotIntoPromise = (promise, plot, content) => {
                    promise = promise.then(() => {
                        if (plot.commands) {
                            content.cmdInfo = { commands: plot.commands };
                            content.cIndex = 0;
                            return getInCmdInfo(content, false);
                        } else {
                            return getInPlot(plot, content.preloadType);
                        }
                    }).then(() => {
                        if (isMaxSize(true)) {
                            return Promise.reject(content.pIndex);
                        } else if (getPlotOptionCount(plot)) {
                            return preloadPlotOptions(content);
                        }else if(plot.jump){
                            this.pushLoadMarks([createJumpMark(content, plot.jump)]);
                            return Promise.reject(-1);
                        } else if(plot.insert){
                            this.pushLoadMarks([createInsertMark(content, plot.insert)]);
                            return Promise.reject(-1);
                        } else if (plot.back) {
                            return Promise.reject(-1);
                        } 
                        return Promise.resolve();
                    });
                    return promise;
                };

                for (let opIndex = 0; plot.options && opIndex < plot.options.length; opIndex++) {
                    const option = plot.options[opIndex];
                    const newPreloadType = `${preloadType==='play'?'':preloadType}$${plot.uuid}o${opIndex}-`;
                    const optChapter = {plots: option.plots, uuid: chapter.uuid};
                    const temp = { ...content, chapter:optChapter, pIndex: 0, preloadType:newPreloadType };
                    let promise = Promise.resolve();
                    for (let opPlotIndex = 0; opPlotIndex < option.plots.length && opPlotIndex < 5; opPlotIndex++) {
                        const content = { ...temp, pIndex:opPlotIndex };
                        content.context = { ...context };
                        promise = loadPlotIntoPromise(promise, option.plots[opPlotIndex], content);
                    }
                    promise = promise.then(() => {
                        if (option.plots.length > 5) {
                            pushMark({ ...temp, pIndex:4 }, opMarks);
                        }
                    }, index => {
                        if (index >= 0 && option.plots.length > index && !option.plots[index].commands) {
                            pushMark({ ...temp, pIndex: index }, opMarks);
                        }
                        return Promise.resolve();
                    });
                    optionPromises.push(promise);
                }

                for (let opIndex = 0; plot.condition_options && opIndex < plot.condition_options.length; opIndex++) {
                    let option = plot.condition_options[opIndex];
                    const optChapter = {plots: option.plots, parent: {pChapter: chapter, pnPlot: pIndex}, uuid: chapter.uuid};
                    const type = option.type !== 'branch' && this.checkCondOptionResources(option) <= 50 ? 'l' : 'c';
                    const newPreloadType = `${preloadType==='play'?'':preloadType}$${plot.uuid}${type}${opIndex}-`;
                    const temp = { ...content, chapter:optChapter, pIndex: 0, preloadType:newPreloadType };
                    let promise = Promise.resolve();
                    for (let opPlotIndex = 0; opPlotIndex < option.plots.length && opPlotIndex < 5; opPlotIndex++) {
                        const content = { ...temp, pIndex:opPlotIndex };
                        content.context = { ...context };
                        promise = loadPlotIntoPromise(promise, option.plots[opPlotIndex], content);
                    }
                    promise = promise.then(() => {
                        if (option.plots.length > 5) {
                            pushMark({ ...temp, pIndex:4 }, copMarks);
                        }
                    }, index => {
                        if (index >= 0 && option.plots.length > index && !option.plots[index].commands) {
                            pushMark({ ...temp, pIndex: index }, copMarks);
                        }
                        return Promise.resolve();
                    });
                    condOptionPromises.push(promise);
                }

                const optionPromise = Promise.all(optionPromises).then(() => opMarks.length && this.pushLoadMarks(opMarks));
                const condoptionPromise = Promise.all(condOptionPromises).then(() => copMarks.length && this.pushLoadMarks(copMarks));
                return Promise.all([optionPromise, condoptionPromise]);
            };

            const getInNextPlot = (content) => {
                let { context, chapter, pIndex } = content;
                const plot = chapter.plots[pIndex];
                if(isMaxSize()){
                    const mark = createMark(content);
                    this.pushLoadMarks([mark]);
                    return Promise.resolve();
                }else if(plot.back){
                    return Promise.resolve();
                }else if(plot.jump){
                    this.pushLoadMarks([createJumpMark(content, plot.jump)]);
                    return Promise.resolve();
                } else if(plot.insert){
                    this.pushLoadMarks([createInsertMark(content, plot.insert)]);
                    return getInChapter({ ...content, pIndex: pIndex + 1 });
                } else if (getPlotOptionCount(plot)) {
                    return preloadPlotOptions(content).then(() => getInChapter({ ...content, pIndex: pIndex + 1 }));
                } else if (chapter.plots.length > pIndex + 1 ) {
                    return getInChapter({ ...content, pIndex: pIndex + 1 });
                } else if (context.stack.length) {
                    context.stack = context.stack.slice(1);
                    if(chapter.parent){
                        pIndex = chapter.parent.pnPlot + 1;
                        chapter = chapter.parent.pChapter;
                    }
                    if (chapter && Number.isInteger(pIndex)) {
                        return getInChapter({ ...content, chapter, pIndex});
                    }
                }
                return Promise.resolve();
            };

            return getInPlot(plot, preloadType).then(() => getInNextPlot(content));
        };

        const checkResources = (resources) => {
            if(resources.size < this.MaxSize){
                this.PreloadState.size = this.MaxSize - resources.size;
            }

            if(this.PreloadState.size > 0){
                this.startPreloadResources();
            }
            return resources;
        }

        const content = { context, chapter, pIndex, cmdInfo, cIndex, preloadType:'play' };
        return cmdInfo
            ? getInCmdInfo(content).then(() => checkResources(resources))
            : getInChapter(content).then(() => checkResources(resources));
    }

    static getResourcesSize(resources) {
        const promises = [];
        resources.forEach(r => {
            promises.push(xhr.getResourceSize(r.key, r.type))
        });
        return Promise.all(promises).then(sizes => sizes.reduce((a,b) => a + b, 0));
    }

    static updateUsingKey({type, key, index}){
        let oldKey = '';
        switch(type){
            case 'background':
                this.usingKeyMap.background = key;
                break;
            case 'preBackground':
                this.usingKeyMap.preBackground = key;
                break;
            case 'music':
                oldKey = this.usingKeyMap.music;
                this.usingKeyMap.music = key;
                oldKey && this.subRefCount('audio', oldKey);
                break;
            case 'sound':
                oldKey = this.usingKeyMap.sounds[index];
                this.usingKeyMap.sounds[index] = key;
                oldKey && this.subRefCount('audio', oldKey);
                break;
        }
    }

    static isUsingKey(key){
        const { background, music, sounds, preBackground } = this.usingKeyMap;
        return background === key || preBackground === key || music === key || sounds.some(s => s === key);
    }
}

