import Utils from '../../common/Utils.js';

export function HtmlTextBox(game,x,y,width,height,text,color,size,family,bold)
{
    Phaser.Group.call(this,game);

    this.game = game;
    this.name = 'input';
    this.parentElement = game.canvas.parentNode;

    //创建一个inputElement
    this.textBoxElement = document.createElement('input');
    this.textBoxElement.style.position = 'absolute';
    this.textBoxElement.style.margin = '0';
    this.textBoxElement.style.border = '0';
    this.textBoxElement.style.padding = '0';
    this.textBoxElement.style.left = x+'px';
    this.textBoxElement.style.top = y+'px';
    this.textBoxElement.style.width = width+'px';
    this.textBoxElement.style.height = height+'px';
    this.textBoxElement.style.color = color;
    this.textBoxElement.style.backgroundColor = 'transparent';
    this.textBoxElement.style.borderColor = 'transparent transparent transparent transparent';
    this.textBoxElement.style.outline = 'none';
    this.textBoxElement.style.fontSize = size + 'px';
    this.textBoxElement.style.fontFamily = family;
    this.textBoxElement.style.fontWeight = bold ? 'bold' : 'normal';
    this.parentElement.insertBefore(this.textBoxElement,game.canvas);

    this._x = x;
    this._y = y;
    this._width = width;
    this._height = height;
    this._fontSize = size;
    this.text = text;
    this.isValue = false;

    game.add.existing(this);
    this.onDestroy.add(function(group)
    {
        //删除界面上的Element
        this.parentElement.removeChild(this.textBoxElement);
    },this);
    //this.update();
}

HtmlTextBox.prototype = Object.create(Phaser.Group.prototype);
HtmlTextBox.prototype.constructor = HtmlTextBox;
//更新函数，用来重新计算TextBox的位置
HtmlTextBox.prototype.update  = function()
{
    let canvas = this.game.canvas;

    let canvasX = canvas.offsetLeft;
    let canvasY =  canvas.offsetTop;
    let canvasWidth = canvas.offsetWidth;
    let canvasHeight = canvas.offsetHeight;

    if (this.oldCanvasWidth == canvasWidth && this.oldCanvasHeight == canvasHeight
        && this.oldCanvasX == canvasX && this.oldCanvasY == canvasY) {
        return;
    }

    let gameWidth = this.game.world.width;
    let gameHeight = this.game.world.height;

    if (canvas.offsetWidth > canvas.offsetHeight) {
        let widthScale = (flashvars.vertical ? canvasHeight : canvasWidth)/gameWidth;
        let heightScale = (flashvars.vertical ? canvasWidth : canvasHeight)/gameHeight;

        let xScale = this._x/gameWidth;
        let yScale = this._y/gameHeight;

        let newX = flashvars.vertical ? (canvasX + canvasWidth - canvasWidth * yScale) : (canvasX + canvasWidth * xScale);
        let newY = flashvars.vertical ? (canvasY + canvasHeight * xScale) : (canvasY + canvasHeight * yScale);
        let newWidth = this._width * widthScale;
        let newHeight = this._height * heightScale;
        let fontSize = this._fontSize * heightScale;


        this.setLeft(flashvars.vertical ? (newX - newWidth/2 - newHeight/2) : newX);
        this.setTop(flashvars.vertical ? (newY + newWidth / 2 - newHeight / 2) : newY);
        this.setWidth(newWidth);
        this.setHeight(newHeight);
        this.setFontSize(fontSize);
        this.textBoxElement.style.transform = flashvars.vertical ? 'rotate(90deg)' : 'rotate(0deg)';
    }
    else {
        let widthScale = (flashvars.vertical ? canvasWidth : canvasHeight)/gameWidth;
        let heightScale = (flashvars.vertical ? canvasHeight : canvasWidth)/gameHeight;

        let xScale = this._x/gameWidth;
        let yScale = this._y/gameHeight;

        let newX = flashvars.vertical ? (canvasX + canvasWidth * xScale) : (canvasX + canvasWidth - canvasWidth * yScale);
        let newY = flashvars.vertical ? (canvasY + canvasHeight * yScale) : (canvasY + canvasHeight * xScale);
        let newWidth = this._width * widthScale;
        let newHeight = this._height * heightScale;
        let fontSize = this._fontSize * heightScale;

        this.setLeft(flashvars.vertical ? newX : (newX - newWidth/2 - newHeight/2));
        this.setTop(flashvars.vertical ? newY : (newY + newWidth / 2 - newHeight / 2));
        this.setWidth(newWidth);
        this.setHeight(newHeight);
        this.setFontSize(fontSize);

        this.textBoxElement.style.transform = flashvars.vertical ? 'rotate(0deg)' : 'rotate(90deg)';
    }

    this.oldCanvasWidth = canvasWidth;
    this.oldCanvasHeight = canvasHeight;
    this.oldCanvasX = canvasX;
    this.oldCanvasY = canvasY;
};

HtmlTextBox.prototype.destroy = function () {
    this.parentElement.removeChild(this.textBoxElement);
};

HtmlTextBox.prototype.show = function (bShow) {
    this.textBoxElement.style.visibility = bShow ? 'visible' : 'hidden';
};

HtmlTextBox.prototype.setInputCallback = function (callback) {
    const inputUpdate = () => {
        this._text = this.textBoxElement.value;
        Utils.sensitiveWords.forEach(word => {
            if (word.length === 0) return;

            let index = this._text.indexOf(word);
            if (index !== -1) {
                let rw = '*'.repeat(word.length);
                this._text = this._text.replace(new RegExp(word, 'gi'), rw);
                this.textBoxElement.value = this._text;
            }
        })
        callback(this._text);
    };
    if (flashvars.client === 'ios' || navigator.userAgent.indexOf('iPhone') > -1) {
        /*this.textBoxElement.addEventListener('compositionstart', ()=>{
            this.textBoxElement.style.backgroundColor = "black";
        });
        this.textBoxElement.addEventListener("compositionupdate", () => {
            this.textBoxElement.style.backgroundColor = "red"; //开始编辑，背景变成黄色
        });*/
        let agentV = navigator.userAgent.match(/CPU\siPhone\sOS\s((([0-9]*)+(_))*)+([0-9]*)/gi);
        if (agentV && (agentV[0] >= 'CPU iPhone OS 10_3_1' || agentV[0] === 'CPU iPhone OS 10_0_2')) {
            this.textBoxElement.addEventListener("input", inputUpdate, false);
        } else {
            this.textBoxElement.addEventListener('compositionend', () => {
                this.textBoxElement.removeEventListener("input", inputUpdate, false);
                this.textBoxElement.addEventListener("input", inputUpdate, false);
            }, false);
        }
    } else {
        this.textBoxElement.addEventListener("input", inputUpdate, false);
    }

    this.textBoxElement.onblur = ()=> {
        if (this.isValue) {
            let v = parseFloat(this._text);
            v ? this.text = v + '' : this.text = '0';
        }
    }
};

HtmlTextBox.prototype.setClickCallback = function (callback) {
    this.textBoxElement.addEventListener('click', () => {callback(this);}, false);
};

//设置位置X
HtmlTextBox.prototype.setLeft = function (left) {
    this.textBoxElement.style.left = left+'px';
};

HtmlTextBox.prototype.setTop = function (top) {
    this.textBoxElement.style.top = top+'px';
};

//设置width
HtmlTextBox.prototype.setWidth = function(width) {
    this.textBoxElement.style.width = width+'px';
};

//设置height
HtmlTextBox.prototype.setHeight = function(height) {
    this.textBoxElement.style.height = height+'px';
};

// 设置字体大小
HtmlTextBox.prototype.setFontSize = function (fontSize) {
    this.textBoxElement.style.fontSize = fontSize + 'px';
};

// Enable
HtmlTextBox.prototype.setEnable = function (bEnabled) {
    this.textBoxElement.readOnly = (!!bEnabled).toString();
};

//使用属性的方式操作X
Object.defineProperty(HtmlTextBox.prototype,'x',
    {
        get:function()
        {
            return this._x;
        },
        set:function(value)
        {
            this.setLeft(value);
            this._x = value;
        }
    });
//使用属性的方式操作Y
Object.defineProperty(HtmlTextBox.prototype,'y',
    {
        get:function()
        {
            return this._y;
        },
        set:function(value)
        {
            this.setTop(value);
            this._y = value;
        }
    });

//使用属性的方式操作width
Object.defineProperty(HtmlTextBox.prototype,'width',
    {
        get:function()
        {
            return this._width;
        },
        set:function(value)
        {
            this.setWidth(value);
            this._width = value;
        }
    });

//使用属性的方式操作height
Object.defineProperty(HtmlTextBox.prototype,'height',
    {
        get:function()
        {
            return this._height;
        },
        set:function(value)
        {
            this.setHeight(value);
            this._height = value;
        }
    });

//使用属性的方式操作fontSize
Object.defineProperty(HtmlTextBox.prototype,'fontSize',
    {
        get:function () {
            return this._fontSize;
        },
        set:function (value) {
            this.setFontSize(value);
            this._fontSize = value;
        }
    });

//使用属性的方式操作style
Object.defineProperty(HtmlTextBox.prototype,'style',
    {
        get:function()
        {
            return this._style;
        },
        set:function(value)
        {
            for(var key in value)
            {
                this.textBoxElement.style[key] = value[key];
            }
            this._style = value;
        }
    });

//设置字体
Object.defineProperty(HtmlTextBox.prototype,'text',
    {
        get:function()
        {
            return this._text;
        },
        set:function(value)
        {
            this.textBoxElement.value = value;
            this._text = value;
        }
    });

// enable
Object.defineProperty(HtmlTextBox.prototype,'readOnly',
    {
        get:function()
        {
            return this.readOnly;
        },
        set:function(value)
        {
            this.setEnable(value);
            this.textBoxElement.readOnly = (!!value).toString();
        }
    });