import Utils from "./Utils";

const urls = {
    getGame : "https://mapi.3000api.com/apis/soft/v1.0/game-info.html",
    getTestGame: "https://mapi.3000api.com/apis/soft/v1.0/game-test.html",
    getOtherGame: "https://mapi.3000api.com/apis/soft/v1.0/game-external.html",
    materialRedirect:"https://mapi.3000api.com/client/material-redirect.html"
};

function serializeQuery(params) {
    params['_'] = Date.now();
    return '?' + Object.keys(params).map(key => {
            const val = params[key];
            if (val) {
                return encodeURIComponent(key) + '=' + encodeURIComponent(val)
            }
            else {
                return '';
            }
        }).join('&');
}

export function xhrGetServerTime(url) {
    return new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest();
        xhr.open("GET", url, true);

        xhr.onload = function () {
            try {
                if (xhr.readyState === 4 && xhr.status >= 400 && xhr.status <= 599) { // Handle HTTP status codes of 4xx and 5xx as errors, even if xhr.onerror was not called.
                    return reject(`error readyState: ${xhr.readyState}, status: ${xhr.status}`);
                }
                else {
                    return resolve(xhr);
                }
            } catch (e) {
                reject(e)
            }
        };

        xhr.onerror = reject;
        xhr.send();
    })
}

export function xhrSend(url, data, onProgress, timeout) {
    return new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest();
        xhr.ontimeout = function () {
            console.log(url + ' time out');
        };
        xhr.onreadystatechange = function() {
            switch(xhr.readyState) {
                case 4 :
                    if((xhr.status >= 200 && xhr.status < 300) || xhr.status == 304) {
                        return resolve(xhr);
                    }
                    else {
                        return reject(`error readyState: ${xhr.readyState}, status: ${xhr.status}`);
                    }
                    break;
            }
        };
        xhr.open("post", url, true);
        xhr.withCredentials = true;
        timeout && (xhr.timeout = timeout);
        onProgress && xhr.addEventListener("progress", onProgress, false);
        xhr.setRequestHeader("CONTENT-TYPE", "application/x-www-form-urlencoded");
        xhr.send(data);
        xhr.onerror = reject;
    });
}

export function xhrLoad(url, type, onProgress,istimeout = true) {
    return new Promise((resolve, reject) => {

        const xhr = new XMLHttpRequest();
        xhr.ontimeout = function () {
            console.log(url + ' time out');
            if(istimeout) return reject("get time out")
        };
        xhr.onload = function () {
            try {
                if (xhr.readyState === 4 && xhr.status >= 400 && xhr.status <= 599) { // Handle HTTP status codes of 4xx and 5xx as errors, even if xhr.onerror was not called.
                    return reject(`error readyState: ${xhr.readyState}, status: ${xhr.status}`);
                }
                else {
                    return resolve(xhr);
                }
            } catch (e) {
                return reject(e)
            }
        };
        xhr.open("GET", url, true);
        xhr.responseType = type;
        onProgress && xhr.addEventListener("progress", onProgress, false);
        istimeout && (xhr.timeout = 5000);//get增加5秒的请求超时
        xhr.onerror = reject;
        xhr.send();
    });
}

export function getJson(url, onProgress) {
    return xhrLoad(url, '', onProgress,false).then(xhr => {
        return JSON.parse(xhr.response);
    });
}

export function getMaterialRedirect(url) {
    return getJson(urls.materialRedirect + serializeQuery({url}))
        .then(json => {
            if (json.code == '100') {
                return json.result;
            }
            else {
                return Promise.reject(json.message);
            }
        });
}

export function getGame(uuid, onProgress) {
    let url = urls.getGame + '?uuid=' + uuid;
    if(flashvars.offline === '1' && flashvars.single){
        //独立
        if(flashvars.offline === '1' && flashvars.single){
            url = Utils.convertSingleJsonPath(uuid);
        }
    }
    return getJson(url, onProgress)
        .then(json => {
            if (json.code == '100') {
                return json.result;
            }
            else if (flashvars.offline === '1') {
                return json;
            }
            else {
                return Promise.reject(json.message);
            }
        });
}

