import puremvc from 'puremvc';
puremvc.define
(
    {
        name: 'diygamePlayer.view.mediator.EndMediator',
        parent: puremvc.Mediator
    },
    {
        listNotificationInterests: function () {
            return [
                diygamePlayer.AppConstants.SCENE_GOTO_END
            ]
        },

        handleNotification: function (note) {
            switch (note.getName()) {
                case diygamePlayer.AppConstants.SCENE_GOTO_END:
                    this.showEndLayer();
                default:
                    break;
            }
        },

        onRegister: function () {
            this.sceneProxy = this.facade.retrieveProxy(diygamePlayer.model.proxy.SceneProxy.NAME);
            const sceneMediator = this.facade.retrieveMediator(diygamePlayer.view.mediator.SceneMediator.NAME);
            this.game = sceneMediator.viewComponent && sceneMediator.viewComponent.game;
        },

        btnStatesChanged: function (b, p, btnObj, txtObj, keyBk, text, inputEnabled) {
            if (txtObj) {
                txtObj.font = text.fontFamily;
                txtObj.fontSize = text.fontSize;
                txtObj.fontWeight = text.fontWeight;
                txtObj.fill = text.color;
                txtObj.visible = !text.hidden;
                if (inputEnabled !== undefined) {
                    txtObj.inputEnabled = inputEnabled;
                }
            }

            this.loadTexture(btnObj, keyBk);
        },

        loadTexture: function (button, key) {
            if (!button) {
                return;
            }
            const {width, height} = button;
            const buttonKey = this.game.cache.checkImageKey(key) ? key : null;
            button.loadTexture(buttonKey);
            button.width = width;
            button.height = height;
        },

        addEnd: function () {

            const addButton = (x, y, key, width, height, name) => {
                const bk_key = this.game.cache.checkImageKey(key) ? key : null;
                const button = this.game.add.button(x, y, bk_key, onBtnClicked, this, 0, 0, 0, 0, this.sceneProxy.endLayer);
                button.width = width;
                button.height = height;
                button.name = name;
                return button;
            };

            const addTextContent = (x, y, width, height, content, objText, name) => {
                const fontStyle = {};
                let ff = '';
                ff += objText.fontWeight ? objText.fontWeight + ' ' : 'normal ';
                ff += objText.fontSize ? parseInt(objText.fontSize) + 'px ' : '';
                ff += objText.fontFamily ? objText.fontFamily : '';

                fontStyle.font = ff;
                fontStyle.fill = objText.color;
                fontStyle.boundsAlignH = "center";
                fontStyle.boundsAlignV = "middle";
                const txt = this.game.add.text(0, 0, content, fontStyle, this.sceneProxy.endLayer);
                txt.name = name;
                txt.setTextBounds(x, y + (txt.height - parseInt(objText.fontSize)) / 2, width, height);
                objText.hidden && (txt.visible = false);
                return txt;
            };

            /*const giveCandy = () => {
                let text = this.sceneProxy.endLayer.getByName("end_text_candy_tip");

                !text && (text = addTextContent(144
                    , 220
                    , 96
                    , 20
                    , "赠送糖果成功(测试)"
                    , {
                        fontFamily: "",
                        fontSize: "12px",
                        color: "#ef7297"
                    }
                    , 'end_text_candy_tip'));
                text.visible = true;
                this.timer && this.game.time.events.remove(this.timer);
                this.timer = this.game.time.events.add(1000, () => {
                    text.visible = false;
                })
            };*/

            const onBtnClicked = (obj, pointer) => {
                if (pointer.pointerMode === Phaser.PointerMode.CURSOR && pointer.button !== 0) {
                    return;
                }
                switch (obj.name) {
                    /*case 'end_button_candy':
                        this.btnStatesChanged(null, null, obj, this.sceneProxy.endLayer.getByName("end_text_candy"), 'icon_candy', text_normal);
                        giveCandy();
                        break;*/
                    case 'end_button_get':
                        this.btnStatesChanged(null, null, obj, this.sceneProxy.endLayer.getByName('end_text_gte'), 'icon_get', text_normal);
                        this.sceneProxy.endLayer.visible = false;
                        this.sceneProxy.visibleLayout = [this.sceneProxy.endLayer, ...this.sceneProxy.visibleLayout];
                        this.sceneProxy.showLoad();
                        break;
                    case 'end_button_back':
                        this.sceneProxy.reStart(this.sceneProxy.isExtend());
                        this.btnStatesChanged(null, null, obj, this.sceneProxy.endLayer.getByName("end_text_back"), 'icon_back', text_normal);
                        this.sceneProxy.endLayer.visible = false;
                        break;
                    case 'end_button_save':
                        this.btnStatesChanged(null, null, obj, this.sceneProxy.endLayer.getByName("end_text_save"), 'icon_save', text_normal);
                        this.sceneProxy.showSave();
                        break;
                    default:
                        break;
                }
            };

            const itemBlackBk = this.game.add.bitmapData(this.game.world.width, this.game.world.height);
            itemBlackBk.fill(0, 0, 0, 0.8);
            const item_bk = this.game.add.sprite(0, 0, itemBlackBk);
            item_bk.width = this.game.world.width;
            item_bk.height = this.game.world.height;
            item_bk.inputEnabled = true;
            this.sceneProxy.endLayer.add(item_bk);

            const text_normal = {
                fontFamily: "",
                fontSize: "14px",
                color: "#ffffff"
            };

            const text_active = {
                fontFamily: "",
                fontSize: "14px",
                color: "#ef7297"
            };

            /*const btn_candy = addButton(144, 100, 'icon_candy', 96, 96, 'end_button_candy');
            const txt_candy = addTextContent(144, 200, 96, 30, "游戏好棒，送颗糖", text_normal, 'end_text_candy');

            btn_candy.onInputOver.add(this.btnStatesChanged, this, 0, btn_candy, txt_candy, 'icon_candy_over_pressed', text_active);
            btn_candy.onInputDown.add(this.btnStatesChanged, this, 0, btn_candy, txt_candy, 'icon_candy_over_pressed', text_active);
            btn_candy.onInputOut.add(this.btnStatesChanged, this, 0, btn_candy, txt_candy, 'icon_candy', text_normal);*/

            const btn_load = addButton(flashvars.vertical ? 55 : 168, flashvars.vertical ? 427 : 100, 'icon_get', 96, 96, 'end_button_get');
            const txt_load = addTextContent(flashvars.vertical ? 55 : 168, flashvars.vertical ? 527 : 200, 96, 30, "读取进度", text_normal, 'end_text_get');

            btn_load.onInputOver.add(this.btnStatesChanged, this, 0, btn_load, txt_load, 'icon_get_hover_pressed', text_active);
            btn_load.onInputDown.add(this.btnStatesChanged, this, 0, btn_load, txt_load, 'icon_get_hover_pressed', text_active);
            btn_load.onInputOut.add(this.btnStatesChanged, this, 0, btn_load, txt_load, 'icon_get', text_normal);

            const btn_back = addButton(flashvars.vertical ? 216 : 432, flashvars.vertical ? 427 : 100, 'icon_back', 96, 96, 'end_button_back');
            const txt_back = addTextContent(flashvars.vertical ? 216 : 432, flashvars.vertical ? 527 : 200, 96, 30, "返回封面", text_normal, 'end_text_back');

            btn_back.onInputOver.add(this.btnStatesChanged, this, 0, btn_back, txt_back, 'icon_back_over_pressed', text_active);
            btn_back.onInputDown.add(this.btnStatesChanged, this, 0, btn_back, txt_back, 'icon_back_over_pressed', text_active);
            btn_back.onInputOut.add(this.btnStatesChanged, this, 0, btn_back, txt_back, 'icon_back', text_normal);

            const btn_save = addButton(flashvars.vertical ? 377 : 696, flashvars.vertical ? 427 : 100, 'icon_save', 96, 96, 'end_button_save');
            const txt_save = addTextContent(flashvars.vertical ? 377 : 696, flashvars.vertical ? 527 : 200, 96, 30, "存个档，下次玩", text_normal, 'end_text_save');

            btn_save.onInputOver.add(this.btnStatesChanged, this, 0, btn_save, txt_save, 'icon_save_over_pressed', text_active);
            btn_save.onInputDown.add(this.btnStatesChanged, this, 0, btn_save, txt_save, 'icon_save_over_pressed', text_active);
            btn_save.onInputOut.add(this.btnStatesChanged, this, 0, btn_save, txt_save, 'icon_save', text_normal);

            this.sceneProxy.endLayer.visible = false;
        },

        showEndLayer: function () {
            this.sceneProxy.getSnap();
            if (this.sceneProxy.endLayer.children.length === 0) {
                this.addEnd()
            }

            this.game.world.bringToTop(this.sceneProxy.endLayer);
            this.sceneProxy.endLayer.visible = true;
        }
    },
    {
        /**
         * @static
         * @type {string}
         */
        NAME: 'EndMediator'
    }
);