import puremvc from 'puremvc';
import FontUtils from '../../common/FontUtils.js';
puremvc.define
(
    {
        name: 'diygamePlayer.view.mediator.MainMenuMediator',
        parent: puremvc.Mediator
    },
    {
        listNotificationInterests: function () {
            return [
                diygamePlayer.AppConstants.SCENE_GOTO_MAINMENU
            ]
        },

        handleNotification: function (note) {
            const body = note.getBody();
            switch (note.getName()) {
                case diygamePlayer.AppConstants.SCENE_GOTO_MAINMENU:
                    this.showMainMenuLayer(body);
                default:
                    break;
            }
        },

        onRegister: function () {
            this.sceneProxy = this.facade.retrieveProxy(diygamePlayer.model.proxy.SceneProxy.NAME);
            this.sceneMediator = this.facade.retrieveMediator(diygamePlayer.view.mediator.SceneMediator.NAME);
            this.game = this.sceneMediator.viewComponent && this.sceneMediator.viewComponent.game;
            this.viewComponent = this.sceneMediator.viewComponent;
            this.isAllHide = false;
        },
        /**
         * 添加系统菜单
         */
        addMainMenu: function () {
            const {background, header, save, load, playback, settings, autoplay, back} = this.sceneProxy.json_data.template.json.mainMenu;

            const addImgFontText = (textInfo, x, y, width, height, content, textObj) => {
                const fontSize = textInfo.fontSize ? parseInt(textInfo.fontSize) : 25;
                const color = textInfo.color ? textInfo.color : '#fefefe';

                const fh = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontHeight(fontSize) : fontSize;
                const fw = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontWidth(fontSize) : fontSize;
                const w = fw * content.length;
                const l = x + (width - w) / 2;
                const t = y + (height - fh) / 2;
                const txtImg = this.sceneProxy.addRetroText(l, t, w, fh, content);
                txtImg.tint = (color === '#ffffff' ? '#fefefe' : color).replace('#', '0x');

                textObj.addChild(txtImg);
                textObj.text = ' ';
            };
            const addTextContent = (x, y, width, height, content, objText, name) => {
                const fontStyle = {};
                let ff = '';
                ff += objText.fontWeight ? objText.fontWeight + ' ' : 'normal ';
                ff += objText.fontSize ? parseInt(objText.fontSize) + 'px ' : '';
                ff += this.sceneProxy.json_data.font ? this.sceneProxy.json_data.font.fontFamily : objText.fontFamily ? objText.fontFamily : '';

                fontStyle.font = ff;
                fontStyle.fill = objText.color;
                fontStyle.boundsAlignH = "center";
                fontStyle.boundsAlignV = "middle";
                fontStyle.stroke = objText.stroke;
                fontStyle.strokeThickness = objText.stroke ? 2 : 0;

                const txt = this.viewComponent.game.add.text(0, 0, content, fontStyle, this.sceneProxy.mainMenuLayer);
                txt.name = name;
                if (FontUtils.fontFamily) {
                    addImgFontText(objText, x, y, width, height, content, txt);
                }
                else {
                    txt.setTextBounds(x, y + (txt.height - parseInt(objText.fontSize)) / 2, width, height);
                }

                objText.hidden && (txt.visible = false);
                return txt;
            };
            const addButton = (obj, name, text) => {
                const t = {};
                t.normal = obj.normal.text;
                t.active = obj.active.text;
                t.text = text;
                const btnGroup = this.viewComponent.addButton(obj.rect.x,
                    obj.rect.y,
                    obj.rect.width,
                    obj.rect.height,
                    obj.normal.background.url,
                    obj.active.background.url,
                    onBtnClicked,
                    t,
                    this.sceneProxy.btn_sound,
                    name,
                    this.sceneProxy.mainMenuLayer
                );
                btnGroup.button.onInputUp.add(() => this.sceneProxy.btn_sound && this.sceneProxy.btn_sound.stop())
            };

            const onBtnClicked = (obj, pointer) => {
                if (pointer.pointerMode === Phaser.PointerMode.CURSOR && pointer.button !== 0) {
                    return;
                }
                if (obj.parent.name === 'mainMenu_button_save') {
                    // 保存进度
                    this.sceneProxy.onMainMenuSaveBtnClicked();
                }
                else if (obj.parent.name === 'mainMenu_button_load') {
                    // 读取进度
                    this.sceneProxy.onMainMenuLoadBtnClicked();
                }
                else if (obj.parent.name === 'mainMenu_button_playback') {
                    // 剧情回放
                    this.sceneProxy.onMainMenuPlaybackBtnClicked();
                }
                else if (obj.parent.name === 'mainMenu_button_autoplay') {
                    // 自动播放
                    this.sceneProxy.onMainMenuAutoplayBtnClicked();
                }
                else if (obj.parent.name === 'mainMenu_button_settings') {
                    // 游戏设置
                    this.sceneProxy.onMainMenuSettingsBtnClicked();
                }
                else if (obj.parent.name === 'mainMenu_button_back') {
                    // 返回
                    this.sceneProxy.onMainMenuBackBtnClicked();
                }
            };

            const bk = this.viewComponent.game.add.image(0, 0, this.viewComponent.game.load.cache.checkImageKey(background.url) && background.url);
            bk.width = this.viewComponent.game.world.width;
            bk.height = this.viewComponent.game.world.height;
            bk.inputEnabled = true;
            this.sceneProxy.mainMenuLayer.add(bk);

            // header
            if (this.viewComponent.game.load.cache.checkImageKey(header.background.url)) {
                const head = this.sceneProxy.mainMenuLayer.create(header.rect.x
                    , header.rect.y
                    , header.background.url);
                head.width = header.rect.width;
                head.height = header.rect.height;
            }
            if (!header.title.text.hidden) {
                addTextContent(header.title.rect.x + header.rect.x
                    , header.title.rect.y + header.rect.y
                    , header.title.rect.width
                    , header.title.rect.height
                    , this.sceneProxy.stringMap.mainMenu
                    , header.title.text
                    , 'mainMenu_text_header');
            }

            // 按钮-保存进度
            if (!save.hidden) {
                addButton(save, 'mainMenu_button_save', this.sceneProxy.stringMap.save);
            }

            // 按钮-读取进度
            if (!load.hidden) {
                addButton(load, 'mainMenu_button_load', this.sceneProxy.stringMap.load);
            }

            // 按钮-剧情回放
            if (!playback.hidden) {
                addButton(playback, 'mainMenu_button_playback', this.sceneProxy.stringMap.playback);
            }

            // 按钮-自动播放
            if (!autoplay.hidden) {
                addButton(autoplay, 'mainMenu_button_autoplay', this.sceneProxy.stringMap.autoplay);
            }

            // 按钮-游戏设置
            if (!settings.hidden) {
                addButton(settings, 'mainMenu_button_settings', this.sceneProxy.stringMap.settings);
            }

            // 按钮-返回
            if (!back.hidden) {
                addButton(back, 'mainMenu_button_back', this.sceneProxy.stringMap.back);
            }
            this.sceneProxy.mainMenuLayer.visible = false;
            if(save.hidden && load.hidden && playback.hidden && settings.hidden && back.hidden){
                //系统菜单项隐藏 点击菜单无效
                this.isAllHide = true;
            }
        },
        /***
         * 添加diy菜单
         */
        addDiyMainMenuenu:function () {
            const {mainMenu: mainMenuView} = this.sceneProxy.json_data.default_settings;
            let bk = this.viewComponent.game.add.graphics(0, 0, this.sceneProxy.mainMenuLayer);
            bk.beginFill(0x0, 0);
            bk.drawRect(0, 0, this.viewComponent.game.world.width, this.viewComponent.game.world.height);
            bk.endFill();
            bk.inputEnabled = true;
            this.sceneMediator.registerViewKey(mainMenuView.ref, mainMenuView.events, this.sceneProxy.plotViewKeys);
            let menuView = this.sceneMediator.addView(this.sceneProxy.mainMenuLayer, mainMenuView.ref, mainMenuView.instanceUuid
                , mainMenuView.x, mainMenuView.y, mainMenuView.events,false,false,true);
            menuView.isMenu = true;
            // 找出菜单中添加的计时器
            const getTimerInMenu = ()=> {
                const visitInChild = view => {
                    if (view.viewType === 'TIMER') {
                        this.sceneProxy.menuTimer[this.sceneProxy.menuTimer.length] = view.name;
                    }
                    view.children.forEach(v => {visitInChild(v)});
                };
                menuView.children.forEach(v => {visitInChild(v)});
            };
            getTimerInMenu();
            this.sceneMediator.uiOnLoadInvoker();
        },

        showMainMenuLayer: function (context) {
            if (this.sceneProxy.mainMenuLayer.children.length > 0 && this.sceneProxy.json_data.default_settings.mainMenu){
                //diy菜单
                this.sceneProxy.mainMenuLayer.removeAll(true);
            }
            if (this.sceneProxy.mainMenuLayer.children.length === 0) {
                if (this.sceneProxy.json_data.default_settings.mainMenu) {
                    this.addDiyMainMenuenu();
                } else {
                    this.addMainMenu();
                }
            }
            const autoButton = this.sceneProxy.mainMenuLayer.getByName('mainMenu_button_autoplay');
            const autoText = autoButton ? autoButton.getByName("text") : undefined;
            if (context.auto) {
                if (autoText) {
                    if (FontUtils.fontFamily) {
                        const txtImgOld = autoText.children[0];
                        if (txtImgOld) {
                            //autoText.children[0].fontImg.text = this.sceneProxy.stringMap.manualPlay;

                            const {autoplay} = this.sceneProxy.json_data.template.json.mainMenu;
                            const fh = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontHeight(autoplay.normal.text.fontSize) : autoplay.normal.text.fontSize;
                            const fw = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontWidth(autoplay.normal.text.fontSize) : autoplay.normal.text.fontSize;
                            const w = fw * this.sceneProxy.stringMap.manualPlay.length;
                            const x = (autoplay.rect.width - w) / 2;
                            const y = (autoplay.rect.height - fh) / 2;
                            this.sceneProxy.addImageFontText([{data: this.sceneProxy.stringMap.manualPlay}], fw, fh, autoText, context);
                            autoText.children[0].x = x;
                            autoText.children[0].y = y;
                            txtImgOld.children.forEach(item => item.fontImg.destroy());
                            txtImgOld.destroy();
                            autoText.text = ' ';
                        }
                    }
                    else {
                        autoText.text = this.sceneProxy.stringMap.manualPlay;
                    }
                }
            }
            else {
                if (autoText) {
                    if (FontUtils.fontFamily) {
                        const txtImgOld = autoText.children[0];
                        if (txtImgOld) {
                            //autoText.children[0].fontImg.text = this.sceneProxy.stringMap.autoplay;

                            const {autoplay} = this.sceneProxy.json_data.template.json.mainMenu;
                            const fh = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontHeight(autoplay.normal.text.fontSize) : autoplay.normal.text.fontSize;
                            const fw = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontWidth(autoplay.normal.text.fontSize) : autoplay.normal.text.fontSize;
                            const w = fw * this.sceneProxy.stringMap.autoplay.length;
                            const x = (autoplay.rect.width - w) / 2;
                            const y = (autoplay.rect.height - fh) / 2;
                            this.sceneProxy.addImageFontText([{data: this.sceneProxy.stringMap.autoplay}], fw, fh, autoText, context);
                            autoText.children[0].x = x;
                            autoText.children[0].y = y;
                            txtImgOld.children.forEach(item => item.fontImg.destroy());
                            txtImgOld.destroy();
                            autoText.text = ' ';
                        }
                    }
                    else {
                        autoText.text = this.sceneProxy.stringMap.autoplay;
                    }
                }
            }
            this.viewComponent.game.world.bringToTop(this.sceneProxy.mainMenuLayer);
            this.sceneProxy.mainMenuLayer.visible = true;
            if(this.isAllHide !== false){
                this.sceneProxy.onMainMenuBackBtnClicked();
            }
        }
    },
    {
        /**
         * @static
         * @type {string}
         */
        NAME: 'MainMenuMediator'
        ,isAllHide : false
    }
);