import puremvc from 'puremvc';
import FontUtils from '../../common/FontUtils.js';
import PhoneUtils from '../../common/PhoneUtils.js';

puremvc.define
(
    {
        name: 'diygamePlayer.view.mediator.CoverMediator',
        parent: puremvc.Mediator
    },
    {
        listNotificationInterests: function () {
            return [
                diygamePlayer.AppConstants.SCENE_GOTO_COVERMENU
            ]
        },

        handleNotification: function (note) {
            switch (note.getName()) {
                case diygamePlayer.AppConstants.SCENE_GOTO_COVERMENU:
                    this.showCoverLayer();
                    break;
                default:
                    break;
            }
        },

        onRegister: function () {
            this.sceneProxy = this.facade.retrieveProxy(diygamePlayer.model.proxy.SceneProxy.NAME);
            const sceneMediator = this.facade.retrieveMediator(diygamePlayer.view.mediator.SceneMediator.NAME);
            this.game = sceneMediator.viewComponent && sceneMediator.viewComponent.game;
            this.viewComponent = sceneMediator.viewComponent;
        },

        addCover: function () {
            const {template, cover} = this.sceneProxy.json_data;
            const {titleContainer, startMenu, loadMenu, settingsMenu, overMenu} = template.json.cover;
            const worldWidth = this.game.world.width;
            const worlHeight = this.game.world.height;

            const addTextContent = (x, y, width, height, content, objText, name, isTitle) => {
                const fontStyle = {};
                let ff = '';
                ff += objText.fontWeight ? objText.fontWeight + ' ' : 'normal ';
                ff += objText.fontSize ? parseInt(objText.fontSize) + 'px ' : '';
                ff += objText.fontFamily ? objText.fontFamily : '';

                fontStyle.font = ff;
                fontStyle.fill = objText.color;
                fontStyle.stroke = objText.stroke;
                fontStyle.strokeThickness = objText.stroke ? 2 : 0;

                fontStyle.boundsAlignH = "center";
                fontStyle.boundsAlignV = isTitle ? "bottom" : "middle";
                const txt = this.game.add.text(0, 0, content, fontStyle, this.sceneProxy.coverLayer);
                txt.name = name;
                if (FontUtils.fontFamily) {
                    const color = (objText.color === '#ffffff' ? '#fefefe' : objText.color).replace('#', '0x');
                    const fontSize = objText.fontSize ? parseInt(objText.fontSize) : 25;
                    txt.colors.splice(0, txt.colors.length);
                    txt.text = ' ';
                    txt.fill = color;
                    txt.addColor(color, 0);
                    txt.x = (worldWidth - fontSize * content.length) / 2;
                    txt.y = y;
                    txt.wordWrapWidth = worldWidth;

                    const fh = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontHeight(fontSize) : fontSize;
                    const fw = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontWidth(fontSize) : fontSize;
                    this.sceneProxy.addImageFontText([{data:content}], fw, fh, txt, this.sceneProxy.context);
                }
                else {
                    txt.setTextBounds(x, y + (txt.height - parseInt(objText.fontSize)) / 2, width, height);
                }
                objText.hidden && (txt.visible = false);
                return txt;
            };

            const addButton = (obj, name, text) => {
                const t = {};
                t.normal = obj.normal.text;
                t.active = obj.active.text;
                t.text = text;
                const btnGroup = this.viewComponent.addButton(obj.rect.x,
                    obj.rect.y,
                    obj.rect.width,
                    obj.rect.height,
                    obj.normal.background.url,
                    obj.active.background.url,
                    onBtnClicked,
                    t,
                    this.sceneProxy.btn_sound,
                    name,
                    this.sceneProxy.coverLayer
                );
                btnGroup.button.onInputUp.add(() => this.sceneProxy.btn_sound && this.sceneProxy.btn_sound.stop())
            };

            const onBtnClicked = (obj, pointer) => {
                if (pointer.pointerMode === Phaser.PointerMode.CURSOR && pointer.button !== 0) {
                    return;
                }
                if (obj.parent.name === 'cover_button_start') {
                    // 开始游戏
                    this.sceneProxy.onCoverStartBtnClicked();
                }
                else if (obj.parent.name === 'cover_button_load') {
                    // 加载按钮
                    this.sceneProxy.onCoverLoadBtnClicked();
                }
                else if (obj.parent.name === 'cover_button_settings') {
                    // 设置按钮
                    this.sceneProxy.onCoverSettingBtnClicked();
                }
                else if (obj.parent.name === 'cover_button_over') {
                    // 游戏结束
                    this.sceneProxy.onCoverOverBtnClicked();
                }
            };

            let bk = this.viewComponent.game.add.graphics(0, 0, this.sceneProxy.coverLayer);
            bk.beginFill(0);
            bk.drawRect(0,0,worldWidth,worlHeight);
            bk.endFill();
            /*blackBk.beginFill(0x0);
            blackBk.moveTo(0, 0);
            blackBk.lineTo(worldWidth, 0);
            blackBk.lineTo(worldWidth, worlHeight);
            blackBk.lineTo(0, worlHeight);
            blackBk.lineTo(0, 0);
            blackBk.endFill();
            blackBk.inputEnabled = true;*/

            const backgroundKey = cover.background.url;
            if(this.game.load.cache.checkImageKey(backgroundKey)){
                bk = this.sceneProxy.coverLayer.create(0, 0,backgroundKey);
            }
            this.sceneProxy.prePlotBkKey = this.sceneProxy.lstPlotBkKey;
            this.sceneProxy.lstPlotBkKey = cover.background.url;
            this.sceneProxy.lstPlotBkFlex = cover.background.flex;
            this.sceneProxy.lstPlotBkOpacity = cover.background.opacity;
            this.sceneProxy.lstGameBk = this.viewComponent.game.add.sprite(0, 0, this.game.load.cache.checkImageKey(backgroundKey) && backgroundKey);
            bk.inputEnabled = true;
            if (cover.background.flex === 3) {// 填充
                let rate = bk.width / bk.height < worldWidth / worlHeight ? worldWidth / bk.width : worlHeight / bk.height;
                bk.width *= rate;
                bk.height *= rate;
                bk.x = -(bk.width - worldWidth) / 2;
                bk.y = -(bk.height - worlHeight) / 2;
            }
            else if (cover.background.flex === 5) {// 适应
                let rate = 0;
                if (bk.width / bk.height > worldWidth / worlHeight) {
                    rate = worldWidth / bk.width;
                    bk.width *= rate;
                    bk.height *= rate;
                    bk.y = (worlHeight - bk.height) / 2;
                }
                else {
                    rate = worlHeight / bk.height;
                    bk.width *= rate;
                    bk.height *= rate;
                    bk.x = (worldWidth - bk.width) / 2;
                }
            }
            else {
                bk.width = worldWidth;
                bk.height = worlHeight;
            }
            this.sceneProxy.lstGameBk.x = bk.x;
            this.sceneProxy.lstGameBk.y = bk.y;
            this.sceneProxy.lstGameBk.width = bk.width;
            this.sceneProxy.lstGameBk.height = bk.height;
            // 封面标题背景
            if (cover.title.data && !titleContainer.title.text.hidden) {
                this.viewComponent.addFitImageSprint(titleContainer.rect.x,
                    titleContainer.rect.y,
                    titleContainer.rect.width,
                    titleContainer.rect.height,
                    titleContainer.background.url,
                    this.sceneProxy.coverLayer
                );
            }

            const {font_size, color, bold, fontFamily, hidden} = cover.title;
            const titleText = titleContainer.title.text;
            font_size && (titleText.fontSize = font_size);
            color && (titleText.color = color);
            bold !== undefined && (titleText.fontWeight = bold ? 'bold' : 'normal');
            titleText.fontFamily = this.sceneProxy.json_data.font ? this.sceneProxy.json_data.font.fontFamily : fontFamily ? fontFamily : '';
            hidden !== undefined && (titleText.hidden = hidden);

            // 封面标题文字
            addTextContent(titleContainer.title.rect.x + titleContainer.rect.x
                , titleContainer.title.rect.y + titleContainer.rect.y
                , titleContainer.title.rect.width
                , titleContainer.title.rect.height
                , cover.title.data
                , titleText
                , 'cover_text_title'
                , true);

            // 封面菜单-开始游戏
            if (!startMenu.hidden) {
                addButton(startMenu, 'cover_button_start', this.sceneProxy.stringMap.startMenu);
            }

            // 封面菜单-加载档案
            if (!loadMenu.hidden) {
                addButton(loadMenu, 'cover_button_load', this.sceneProxy.stringMap.loadMenu);
            }

            // 封面菜单-环境设置
            if (!settingsMenu.hidden) {
                addButton(settingsMenu, 'cover_button_settings', this.sceneProxy.stringMap.settingsMenu);
            }

            // 封面菜单-游戏结束
            if (!overMenu.hidden) {
                addButton(overMenu, 'cover_button_over', this.sceneProxy.stringMap.overMenu);
            }
            this.sceneProxy.coverLayer.visible = false;
        },

        showCoverLayer: function () {
            this.sceneProxy.lstPlotLayer && this.sceneProxy.lstPlotLayer.destroy();
            this.sceneProxy.curPlotLayer && this.sceneProxy.curPlotLayer.destroy();
            this.sceneProxy.dialogLayer.visible = false;
            this.sceneProxy.menuLayer.visible = false;
            this.sceneProxy.lstGameBk && (this.sceneProxy.lstGameBk.visible = true);
            if (this.sceneProxy.coverLayer.children.length === 0) {
                this.addCover()
            }

            // 封面背景音乐
            const {music, background} = this.sceneProxy.json_data.cover;
            if (this.sceneProxy.bk_music) {
                this.sceneProxy.stopMusic();
            }
            if (music && (this.game.load.cache.checkSoundKey(music.url) || PhoneUtils.isAudioInCache(music.url) && flashvars.client === 'ios' && '1.5.0' <= flashvars.app_version)) {
                this.sceneProxy.playMusic(music.url, music.volume, true);
            }
            this.sceneProxy.lstPlotBkKey = background.url;
            this.sceneProxy.lstPlotBkOpacity = background.opacity;
            this.sceneProxy.lstPlotBkFlex = background.flex;
            this.sceneProxy.coverLayer.visible = true;
            this.game.world.bringToTop(this.sceneProxy.coverLayer);
        }
    },
    {
        /**
         * @static
         * @type {string}
         */
        NAME: 'CoverMediator'
    }
);