import puremvc from 'puremvc';
import MD5 from 'spark-md5';
import Dialog from './../../common/Dialog.js';
import Utils from './../../common/Utils.js';
import FontUtils from '../../common/FontUtils.js';

puremvc.define
(
    {
        name: 'diygamePlayer.view.mediator.RecordMediator',
        parent: puremvc.Mediator
    },
    {
        listNotificationInterests: function () {
            return [
                diygamePlayer.AppConstants.SCENE_GOTO_RECORD
            ]
        },

        handleNotification: function (note) {
            const body = note.getBody();
            this.callbackInfo = !!body ? body : (this.sceneProxy.recordLayer ? this.callbackInfo : null);
            switch (note.getName()) {
                case diygamePlayer.AppConstants.SCENE_GOTO_RECORD:
                    this.showRecordLayer();
                    break;
                default:
                    break
            }
        },

        onRegister: function () {
            this.sceneProxy = this.facade.retrieveProxy(diygamePlayer.model.proxy.SceneProxy.NAME);
            const sceneMediator = this.facade.retrieveMediator(diygamePlayer.view.mediator.SceneMediator.NAME);
            this.game = sceneMediator.viewComponent && sceneMediator.viewComponent.game;
            this.viewComponent = sceneMediator.viewComponent;
            this.localMode = true;
            this.isChangeClick = false;
            this.clicktime = 0;
            this.clickchangetime = 0;
        },

        onLocalBtnClicked: function() {
            if(this.clickInterval(800) !== true) return;
            if (!this.localMode) {
                this.localMode = true;
                this.isChangeClick = true;
                const btnLocal = this.sceneProxy.recordLayer.getByName('local_btn');
                const btnCloud = this.sceneProxy.recordLayer.getByName('cloud_btn');
                btnLocal && btnLocal.changeBackground('local_hover');
                btnCloud && btnCloud.changeBackground('cloud');
                this.sceneProxy.onRecordLocalBtnClicked();
            }
        },

        onCloudBtnClicked: function() {
            if(this.clickInterval(800) !== true) return;
            if (this.localMode) {
                this.sceneProxy.isSignedIn().then(() => {
                    this.localMode = false;
                    this.isChangeClick = true;
                    const btnLocal = this.sceneProxy.recordLayer.getByName('local_btn');
                    const btnCloud = this.sceneProxy.recordLayer.getByName('cloud_btn');
                    btnLocal && btnLocal.changeBackground('local');
                    btnCloud && btnCloud.changeBackground('cloud_hover');
                    this.sceneProxy.onRecordCloudBtnClicked();
                }, rj => {
                    let n = rj.indexOf('- status:-1');
                    n = n >= 0 ? n : rj.indexOf('- status:2');
                    if (n >= 0) {
                        this.sceneProxy.context.fullScreen && this.sceneProxy.onSettingsWindowModeBtnClicked();
                        //频繁操作，强行切换到本地存档   先切换 再弹窗;
                        if(rj.indexOf('- status:-110') > 0){
                            const btnLocal = this.sceneProxy.recordLayer.getByName('local_btn');
                            const btnCloud = this.sceneProxy.recordLayer.getByName('cloud_btn');
                            btnLocal && btnLocal.changeBackground("local_hover","local_btn");
                            btnCloud && btnCloud.changeBackground("cloud","cloud_btn");
                            this.sceneProxy.onRecordLocalBtnClicked();
                            this.localMode = true;
                            Dialog.error(rj.substr(0, n)).then(()=>{},()=>{});
                            return;
                        }
                        if (flashvars.app_version >= '1.2.0') {
                            window.location.href = 'lzj3000://logout';
                        }
                        if(flashvars.client === 'wap'){
                            window.parent && window.parent.playLogin();
                        }else{
                            Dialog.error(rj.substr(0, n)).then(()=>{
                                window.location.href = 'lzj3000://login';
                            })
                        }
                    } else if (0 <= rj.indexOf('error')) {
                        this.sceneProxy.context.fullScreen && this.sceneProxy.onSettingsWindowModeBtnClicked();
                        Dialog.error('网络异常，请检查您的网络！');
                    }
                });
            }
        },

        addRecord: function () {
            const {background, scrollBox, back} = this.sceneProxy.json_data.template.json.record;
            let {rect: boxRect, item: boxItem, itemsOffsetX, itemsOffsetY, itemSpaceX: hSpace, itemSpaceY: vSpace} = scrollBox;
            const {rect: itemRect, hint, content} = boxItem;
            const {rect: hintRect, text: hintText} = hint;
            const {rect: contentRect} = content;
            const {x: boxX, y: boxY, width: boxWidth, height: boxHeight} = boxRect;
            const {x: itemX, y: itemY, width: itemWidth, height: itemHeight} = itemRect;
            const {x: hintX, y: hintY, width: hintWidth, height: hintHeight} = hintRect;
            const {x: contentX, y: contentY, width: contentWidth, height: contentHeight} = contentRect;
            hSpace = hSpace === undefined ? 20 : hSpace;
            vSpace = vSpace === undefined ? 12 : vSpace;
            itemsOffsetX = itemsOffsetX === undefined ? 0 : itemsOffsetX;
            itemsOffsetY = itemsOffsetY === undefined ? 0 : itemsOffsetY;

            const fontStyle = {boundsAlignH: 'center', boundsAlignV: 'middle'};
            const addTextContent = (x, y, width, height, content, objText) => {
                let txt;
                let ff = '';
                ff += objText.fontWeight ? objText.fontWeight + ' ' : 'normal ';
                ff += objText.fontSize ? parseInt(objText.fontSize) + 'px ' : '';
                ff += this.sceneProxy.json_data.font ? this.sceneProxy.json_data.font.fontFamily : objText.fontFamily ? objText.fontFamily : '';

                fontStyle.font = ff;
                fontStyle.fill = objText.color;
                txt = this.game.add.text(0, 0, content, fontStyle, this.sceneProxy.recordLayer);

                if (FontUtils.fontFamily) {
                    const color = (objText.color ? objText.color : this.json_data.default_settings.color).replace('#', '0x');
                    if (color.indexOf('rgb') !== -1) {
                        const rgb = color.replace('rgb(', '').replace(')', '').split(',');
                        txt.addColor('#' + ('00000' + (parseInt(rgb[0]) << 16 | parseInt(rgb[1]) << 8 | parseInt(rgb[2])).toString(16)).substr(-6), 0);
                    }
                    else {
                        txt.addColor(color, 0);
                    }

                    const fw = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontWidth(txt.fontSize) : txt.fontSize;
                    txt.x = x + (width - 14 * 14 * fw / 25 - 21 - 22 * fw / 25) / 2;
                    txt.y = y + (txt.height - parseInt(txt.fontSize)) / 2;
                    txt.wordWrapWidth = 20 * 13 * fw / 25;
                }
                else {
                    txt.setTextBounds(x, y + (txt.height - parseInt(objText.fontSize)) / 2, width, height);
                }

                return txt;
            };

            // 背景
            const bk = this.sceneProxy.recordLayer.create(0, 0, this.game.load.cache.checkImageKey(background.url) && background.url);
            bk.width = this.game.world.width;
            bk.height = this.game.world.height;
            bk.inputEnabled = true;

            // scrollBox
            const scrollPane = this.game.add.sprite(0,0);

            const backgroundRect = Object.assign({x: 0, y: 0, width: boxWidth, height: boxHeight}, background.rect);
            const scrollBk = this.sceneProxy.recordLayer.create(boxX + backgroundRect.x,
                boxY + backgroundRect.y,
                this.game.load.cache.checkImageKey(scrollBox.background.url) && scrollBox.background.url);
            scrollBk.width = backgroundRect.width;
            scrollBk.height = backgroundRect.height;

            const scrollX = boxX + itemsOffsetX;
            const scrollY = boxY + itemsOffsetY;
            scrollPane.x = scrollX;
            scrollPane.y = scrollY;
            scrollPane.name = 'scrollPane';
            this.sceneProxy.recordLayer.add(scrollPane);
            // item  横屏 4列3行  竖屏  1列 12行
            const nRow = flashvars.vertical ? 12 : 3;//Math.ceil(boxHeight / itemHeight);
            const nCol = flashvars.vertical ? 1 : 4;//Math.floor(boxWidth / itemWidth);
            let nLeft = 0;
            let nTop = 0;
            let keyItemBk;
            let ptDown = {x: 0, y: 0};
            const dragBk = this.game.add.sprite(0, 0);
            dragBk.width = (itemWidth + hSpace) * nCol;
            dragBk.height = (itemHeight + vSpace) * nRow;
            scrollPane.addChild(dragBk);

            let dragStartPointX = 0;
            let dragStartY = null;
            let isSaveOrGet = true;// 是否读档存档
            const onScroll = (x, y) => {
                if((Math.abs(scrollPane.y - dragStartY) > 2 || Math.abs(scrollPane.x - dragStartPointX) > 2) && (dragStartY)){
                    isSaveOrGet = false;
                }
                const scrollHeight = itemHeight * nRow + vSpace * (nRow - 1);
                if (y > scrollY || scrollHeight <= boxHeight) {
                    scrollPane.y = scrollY;
                }
                else if (y + scrollHeight < boxY + boxHeight) {
                    scrollPane.y = boxY + boxHeight - (itemHeight * nRow + vSpace * (nRow - 1));
                }
                else {
                    scrollPane.y = y;
                }
            };

            const onDragUpdate = (sprite, pointer, x, y, pt, bFromStart) => {
                let px = x;
                let py = y;
                let w = (window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth);
                let h = (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight);
                if (w < h && dragStartY !== null && !flashvars.vertical) {
                    py = dragStartY - x + dragStartPointX;
                }
                onScroll(px, py);
            };

            const onDragStart = (sprite, pointer, x, y, pt, bFromStart) => {
                dragStartPointX = x;
                dragStartY = scrollPane.y;
            };

            const onDragStop = (sprite, pointer, x, y, pt, bFromStart) => {
                dragStartY = scrollPane.y;
            };

            const getPoint = (pointer) => {
                let x, y;
                let w = (window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth);
                let h = (window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight);
                if (w > h) {
                    if(flashvars.vertical){
                        x = pointer.worldY;
                        y = -pointer.worldX;
                    }else{
                        x = pointer.worldX;
                        y = pointer.worldY;
                    }
                }
                else {
                    if(flashvars.vertical){
                        x = pointer.worldX;
                        y = pointer.worldY;
                    }else{
                        x = pointer.worldY;
                        y = -pointer.worldX;
                    }
                }
                if (y < scrollY || y > /*boxY*/scrollY + boxHeight ||
                    x < scrollX || x > /*boxX*/scrollX + boxWidth) {
                    return null;
                }
                return {x, y}
            };

            const onInputDown = (sprite, pointer, x, y) => {
                isSaveOrGet = true;
                if (!getPoint(pointer)) {
                    return sprite.input.disableDrag();
                }
                return sprite.input.enableDrag();
            };

            const onItemUp = (m, pt) => {
                if(isSaveOrGet !== true) return;
                const point = getPoint(pt);
                if (!point || pt.pointerMode === Phaser.PointerMode.CURSOR && pt.button !== 0) {
                    return;
                }
                //防止快速点击，做0.8秒时间间隔
                if(this.clicktime === 0){
                    this.clicktime = this.game.time.now
                }else{
                    if((this.game.time.now - this.clicktime) >= 800){
                        this.clicktime = 0;
                    }else{
                        return
                    }
                }
                ptDown.x = 0;
                ptDown.y = 0;
                const x = point.x - scrollPane.x;
                const y = point.y - scrollPane.y;
                const w = itemWidth + hSpace;
                const h = itemHeight + vSpace;
                if (x % w < itemWidth && y % h < itemHeight) {
                    const iCol = Math.floor(x / w);
                    const iRow = Math.floor(y / h);
                    flashvars.vertical ? this.sceneProxy.onRecordItemClicked(iRow,iCol) : this.sceneProxy.onRecordItemClicked(iCol,iRow);
                    if (!this.sceneProxy.bToSaveFile) {
                        this.callbackInfo = null;
                    }
                }
            };

            const onMouseWheel = e => {
                const delta = e.wheelDelta ? e.wheelDelta / 120 : -e.detail / 3;
                onScroll(0, scrollPane.y + delta * 100)
            };

            let wheelEvent = false;
            const onMove = (pointer, x, y) => {
                const point = getPoint(pointer);
                const mousewheelevt = (/Firefox/i.test(navigator.userAgent)) ? "DOMMouseScroll" : "mousewheel";
                if (!point) {
                    if (wheelEvent) {
                        document.removeEventListener(mousewheelevt, onMouseWheel);
                        wheelEvent = false;
                    }
                }
                else {
                    if (!wheelEvent) {
                        document.addEventListener(mousewheelevt, onMouseWheel);
                        wheelEvent = true;
                    }
                }
            };

            keyItemBk = this.game.load.cache.checkImageKey(boxItem.background.url) ? boxItem.background.url : null;

            for (let r = 0; r < nRow; r++) {
                for (let c = 0; c < nCol; c++) {
                    const itemGroup = this.game.add.group();
                    itemGroup.x = c * (itemWidth + hSpace) + itemX;
                    itemGroup.y = r * (itemHeight + vSpace) + itemY;
                    itemGroup.name = `recordItem_${r * nCol + c}`;
                    const item_bk = this.game.add.sprite(nLeft, nTop, keyItemBk);
                    item_bk.width = itemWidth;
                    item_bk.height = itemHeight;

                    const subItem = this.sceneProxy.recordLayer.create(contentX, contentY, this.game.load.cache.checkImageKey(content.background.url) && content.background.url);		// content
                    subItem.name = 'content';
                    subItem.width = contentWidth;
                    subItem.height = contentHeight;

                    const txtTime = addTextContent(hintX
                        , hintY
                        , hintWidth
                        , hintHeight
                        , ''
                        , hintText);
                    txtTime.name = 'textTime';
                    const itemMask = this.game.add.graphics(0, 0);
                    itemMask.beginFill();
                    itemMask.drawRect(0, 0, itemWidth, itemHeight);
                    itemMask.endFill();
                    itemGroup.addChild(itemMask);
                    itemGroup.mask = itemMask;
                    itemGroup.add(item_bk);
                    itemGroup.add(subItem);
                    itemGroup.add(txtTime);

                    if (this.sceneProxy.json_data.default_settings.autoSaveOrLoad && r === 0 && c === 0) {
                        const autoRecord_Bk = this.game.add.bitmapData(itemWidth, itemHeight);
                        autoRecord_Bk.fill(0, 0, 0, 0.4);
                        const autoRecord = this.game.add.sprite(0, 0, autoRecord_Bk);
                        autoRecord.width = itemWidth;
                        autoRecord.height = itemHeight;
                        const fontStyle = {
                            font: '16px Microsoft YaHei',
                            fill: 'white',
                            boundsAlignH: 'center',
                            boundsAlignV: 'middle'
                        };
                        const autoRecord_txt = this.game.add.text(0, 0, '自动存档', fontStyle, this.sceneProxy.coverLayer);
                        autoRecord_txt.setTextBounds(0, (autoRecord_txt.height - 16) / 2, itemWidth, itemHeight);
                        itemGroup.add(autoRecord);
                        itemGroup.add(autoRecord_txt);
                    }

                    scrollPane.addChild(itemGroup);
                }
            }
            scrollPane.inputEnabled = true;
            scrollPane.input.enableDrag();
            scrollPane.input.allowHorizontalDrag = false;
            scrollPane.events.onDragUpdate.add(onDragUpdate, this);
            scrollPane.events.onDragStart.add(onDragStart, this);
            scrollPane.events.onDragStop.add(onDragStop, this);
            scrollPane.events.onInputUp.add(onItemUp, this);
            scrollPane.events.onInputDown.add(onInputDown, this);
            this.game.input.addMoveCallback(onMove, this);

            // mask
            const mask = this.game.add.graphics(0, 0);
            mask.beginFill();
            mask.drawRect(scrollPane.x, scrollPane.y, boxWidth - itemsOffsetX, boxHeight - itemsOffsetY);
            mask.endFill();
            this.sceneProxy.recordLayer.add(mask);
            scrollPane.mask = mask;

            // 返回按钮
            const t = {};
            t.normal = back.normal.text;
            t.active = back.active.text;
            t.text = this.sceneProxy.stringMap.back;

            const onBtnClicked = (obj, pointer) => {
                if (pointer.pointerMode === Phaser.PointerMode.CURSOR && pointer.button !== 0) {
                    return;
                }
                if ('record_button_back' === obj.parent.name) {
                    this.sceneProxy.onRecordBackBtnClicked();
                    if (this.callbackInfo) {
                        this.callbackInfo.callbackFun(this.callbackInfo.callbackArgs);
                        this.callbackInfo = null;
                    }
                }
                else if ('local_btn' === obj.parent.name) {
                    this.onLocalBtnClicked();
                }
                else if ('cloud_btn' === obj.parent.name) {
                    this.onCloudBtnClicked();
                }
            };

            if (!back.hidden) {
                const btnGroup = this.viewComponent.addButton(back.rect.x,
                    back.rect.y,
                    back.rect.width,
                    back.rect.height,
                    back.normal.background.url,
                    back.active.background.url,
                    onBtnClicked,
                    t,
                    this.sceneProxy.btn_sound,
                    "record_button_back",
                    this.sceneProxy.recordLayer
                );
                btnGroup.button.onInputUp.add(() => this.sceneProxy.btn_sound && this.sceneProxy.btn_sound.stop())
            }
            // 本地--云 按钮
            t.text = '';
            if(this.sceneProxy.isShowLocalBtn()){
                let localx = this.sceneProxy.viewComponent.game.world.width - 74;
                let localy = 25;
                this.viewComponent.addButton(localx, localy, 68, 68
                    , 'local_hover', null
                    , onBtnClicked
                    , t, null, 'local_btn'
                    , this.sceneProxy.recordLayer
                    , true);
            }

            if(this.sceneProxy.isShowCloudBtn()){ //不存在来源wap或wap=3000 、非推广  没有云存档按钮
                let cloundx = this.sceneProxy.viewComponent.game.world.width - 74;
                let cloundy = 107;
                this.viewComponent.addButton(cloundx, cloundy, 68, 68
                    , 'cloud', null
                    , onBtnClicked
                    , t, null, 'cloud_btn'
                    , this.sceneProxy.recordLayer
                    , true);
            }
            this.sceneProxy.recordLayer.visible = false;
        },

        updateItems: function () {
            this.sceneProxy.savedPlotsData.forEach((data, i) => {
                const setPreviewImage = key => {
                    const bKey = this.game.load.cache.checkImageKey(key) ? key : null;
                    const itemContent = item.getByName('content');
                    const {width, height} = itemContent;
                    itemContent.loadTexture(bKey);
                    itemContent.width = width;
                    itemContent.height = height;
                };
                const {timestamp, plotSnap} = data ? data : {};
                const item = this.sceneProxy.recordLayer.getByName('scrollPane').children.find(c => c.name === `recordItem_${i}`);
                if (!item) {
                    return;
                }
                if (!timestamp) {
                    setPreviewImage(this.sceneProxy.json_data.template.json.record.scrollBox.item.content.background.url);
                    item.getByName('textTime').text = '';
                    return;
                }
                const itemTime = item.getByName('textTime');
                const snapMd5 = MD5.hash(plotSnap);

                if (!this.game.load.cache.checkImageKey(snapMd5)) {
                    const data = new Image();
                    data.src = plotSnap;
                    data.onload = e => {
                        if (!this.game.load.cache.checkImageKey(snapMd5)) {
                            this.game.cache.addImage(snapMd5, plotSnap, e.target);
                        }
                        setPreviewImage(snapMd5);
                    };
                } else {
                    setPreviewImage(snapMd5);
                }
                const strText = Utils.getCurrentDateTime(timestamp);
                if (FontUtils.fontFamily) {
                    const fh = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontHeight(itemTime.fontSize) : itemTime.fontSize;
                    const fw = this.sceneProxy.json_data.font.size ? this.sceneProxy.toRealImageFontWidth(itemTime.fontSize) : itemTime.fontSize;
                    this.sceneProxy.addImageFontText([{data:strText}], fw, fh, itemTime, this.sceneProxy.context);
                    itemTime.text = ' ';
                }
                else {
                    itemTime.text = strText;
                }
            });
        },

        showRecordLayer: function () {
            //定位到云存档，如果登入状态，直接显示云存档，没登入显示云存档提示
            this.clicktime = 0;
            if (this.sceneProxy.recordLayer.children.length === 0) {
                this.addRecord();
            }
            //本地和云存档按钮存在，同时云和本地存档按钮未点击
            if(this.sceneProxy.isShowCloudBtn() && this.sceneProxy.isShowLocalBtn() && this.isChangeClick !== true
                || this.isChangeClick !== true && this.sceneProxy.isWapOther()/*第三方来源，定位到云存档*/){
                this.sceneProxy.isSignedIn().then(() => {
                    //登入
                    this.localMode = false;
                    this.isChangeClick = true;
                    const btnLocal = this.sceneProxy.recordLayer.getByName('local_btn');
                    const btnCloud = this.sceneProxy.recordLayer.getByName('cloud_btn');
                    btnLocal && btnLocal.changeBackground('local');
                    btnCloud && btnCloud.changeBackground('cloud_hover');
                    this.sceneProxy.onRecordCloudBtnClicked();
                    return;
                },(rj) =>{
                    //未登入
                    this.updateItems();
                    this.game.world.bringToTop(this.sceneProxy.recordLayer);
                    this.showGuide();
                    this.sceneProxy.recordLayer.visible = true;
                    return;
                })
            }else{
                this.updateItems();
                this.game.world.bringToTop(this.sceneProxy.recordLayer);
                this.showGuide();
                this.sceneProxy.recordLayer.visible = true;
            }
        },
        //显示存档提示
        showGuide:function () {
            if(this.sceneProxy.isShowGuide === true
                && this.sceneProxy.recordLayer.visible !== true
                && !this.sceneProxy.isExtend()
                && !Boolean(flashvars.wap && flashvars.wap === "zxwwap")
                && !this.sceneProxy.isWapOther()){
                this.sceneProxy.showGuideTip(1);
            }
        },
        //点击间隔
        clickInterval:function (times) {
            if(this.clickchangetime === 0){
                this.clickchangetime = this.game.time.now;
                return true;
            }else{
                if((this.game.time.now - this.clickchangetime) >= times){
                    this.clickchangetime = 0;
                    return true;
                }else{
                    return false;
                }
            }
        }
    },

    {
        /**
         * @static
         * @type {string}
         */
        NAME: 'RecordMediator'
        , localMode: true
        , isChangeClick:false //是否已点击切换
        , clicktime:0  //存档点击的间隔时间
        , clickchangetime:0 //存档切换时间间隔
    }
);