/**
 *
 * This is a simple state template to use for getting a Phaser game up
 * and running quickly. Simply add your own game logic to the default
 * state object or delete it and make your own.
 *
 */

// initialize the app when all DOM content has loaded.
import 'babel-polyfill';
import './AppConstants.js';
import './ApplicationFacade.js';
import './controller/command/PrepareControllerCommand.js';
import './controller/command/PrepareModelCommand.js';
import './controller/command/PrepareViewCommand.js';
import './controller/command/StartupCommand.js';
import './model/proxy/SceneProxy.js';
import './view/component/Scene.js';
import './view/mediator/SceneMediator.js';
document.addEventListener('DOMContentLoaded', function () {
    //先获取浏览器显示宽高
    global.width = window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;
    global.height = window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
    // initialize the facade by retrieving an instance and calling startup
    diygamePlayer.ApplicationFacade.getInstance(diygamePlayer.ApplicationFacade.NAME).startup();
    console.log("Initialized");
});