/**
 * @class demo.controller.PrepareW3CViewCommand
 * @extends puremvc.SimpleCommand
 */
import puremvc from 'puremvc';
puremvc.define
(
	// CLASS INFO
	{
		name: 'diygamePlayer.controller.command.PrepareViewCommand',
		parent: puremvc.SimpleCommand
	},
	
	// INSTANCE TRAITS
	{
		/** @override */
		execute: function (note)
		{
			// register the TextComponentMediator
			this.facade.registerMediator( new diygamePlayer.view.mediator.SceneMediator );
		}
	}
);				